.modal {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	overflow-y: auto;
	-webkit-overflow-scrolling: touch;
	visibility: hidden;
	z-index: 100;

	&::before {
		position: fixed;
		content: '';
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: $rgbaWhite;
		transition: opacity .4s ease 0s;
		opacity: 0;
	}

	&._active {
		visibility: visible;

		&::before {
			opacity: 1;
		}
	}

	&__content {
		position: relative;
		width: 100%;
		min-height: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		flex: 1 1 auto;
		@include aPx('padding-top', 45, 35, 1);
		@include aPx('padding-right', 65, 10, 1);
		@include aPx('padding-bottom', 110, 35, 1);
		@include aPx('padding-left', 65, 10, 1);
		z-index: 1;
		@media only screen and (max-width: 768px) {
			justify-content: center;
		}
	}


	&__header {
		width: 100%;
		display: flex;
		justify-content: flex-end;
		align-items: center;
		margin: 0 0 41px;
		@media only screen and (max-width: 768px) {
			flex: 1;
			align-items: flex-start;
		}
	}

	&__close {
		&:before {
			font-size: 24px;
			color: $rgbaGrey-three;
			transition: all .4s ease 0s;
		}
		@media (any-hover: hover) {
			&:hover:before {
				color: rgba(0, 0, 0, 1);
			}
		}
	}

	&__body {
		position: relative;
		display: flex;
		@media only screen and (max-width: 768px) {
			flex: 1;
		}
		@media only screen and (max-width: 750px) {
			flex-direction: column;
			align-items: center;
		}
	}

	&__preview-thumbs {
		width: 130px;
		height: 350px;
		@include adaptive(width, 1024px, 1920px, 61px, 130px);
		@include adaptive(height, 1024px, 1920px, 164px, 350px);
		overflow: hidden;

		.modal__slider-picture {
			@include adaptive(width, 1024px, 1920px, 44px, 93px);
			@include adaptive(height, 1024px, 1920px, 62px, 132px);
			@media only screen and (max-width: 750px) {
				width: 36px;
				height: 50px;
			}
		}

		.modal__slider-wrapper {
			display: flex;
			flex-direction: column;
			align-items: center;
			@media only screen and (max-width: 750px) {
				flex-direction: row;
			}
		}

		.modal__slider-slide {
			display: flex;
			justify-content: center;
			align-items: center;
			border: 1px solid $rgbaGrey-three;

			&.swiper-slide-thumb-active {
				border-color: $black;
			}

			@media only screen and (max-width: 750px) {
				border-radius: 5px;
			}
		}

		@media only screen and (max-width: 750px) {
			width: 110px;
			height: 62px;
			order: 2;
			align-self: flex-start;
		}
	}

	&__viewport {
		@include adaptive(width, 1024px, 1920px, 413px, 603px);
		@include adaptive(height, 1024px, 1920px, 586px, 855px);
		overflow: hidden;
		@include adaptive(margin-left, 1024px, 1920px, 40px, 86px);
		@include adaptive(margin-right, 1024px, 1920px, 40px, 86px);

		.modal__slider-picture {
			width: 100%;
			height: 100%;
		}

		@media only screen and (max-width: 992px) {
			@include adaptive(width, 768px, 992px, 300px, 413px);
			@include adaptive(height, 768px, 992px, 450px, 586px);
			margin: 0 20px;
		}
		@media only screen and (max-width: 750px) {
			width: 350px;
			height: 497px;
			order: 1;
			margin: 0 0 16px 0;
		}
		@media only screen and (max-width: 360px) {
			width: 290px;
			height: 437px;
		}
	}

	&__info {
		max-width: 282px;
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		@media only screen and (max-width: 768px) {
			justify-content: unset;
		}
		@media only screen and (max-width: 750px) {
			max-width: 100%;
			order: 3;
		}
	}

	&__control {
		display: flex;
		align-items: center;
		justify-content: space-between;
		@include aPx('margin-bottom', 130, 32, 1);
		@media only screen and (max-width: 768px) {
			@include adaptive(padding-top, 320px, 768px, 32px, 60px);
		}
	}

	&__control-btn {
		&:before {
			font-weight: 700;
			color: $rgbaGrey-three;
			transition: all .4s ease 0s;
		}

		&--next {
			&:before {
				transform: rotate(180deg);
			}
		}
		@media (any-hover: hover) {
			&:hover:before {
				color: rgba(0, 0, 0, 1);
			}
		}
	}

	&__title {
		font-size: 15px;
		line-height: 140%;
		font-weight: 500;
		margin: 0 0 40px;
	}

	&__text {
		font-size: 15px;
		line-height: 140%;
		font-weight: 400;
		color: $black-a;
		@include aPx('margin-bottom', 90, 32, 1);
	}

	&__counter {
		display: flex;
		align-items: center;

		* {
			font-size: 15px;
			line-height: 140%;
			font-weight: 400;
		}
	}

	&__counter-separator {
		padding: 0 2px;
	}
}
