.top {
	background: url('../images/main-page/img.jpg') no-repeat center/cover;
	&__content {
		display: flex;
		@include aPx('padding-top', 103, 57, 1);
		@include aPx('padding-bottom', 103, 77, 1);
		@media only screen and (max-width: 710px) {
			flex-direction: column;
		}
	}
	&__subtitle {
		font-size: 20px;
		font-weight: 500;
		line-height: 100%;
		color: $white;
		@media only screen and (max-width: 710px) {
			margin: 0 0 60px;
		}
	}
	&__left {
		max-width: 774px;
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		@include aPx('margin-right', 87, 0, 1);
		@media only screen and (max-width: 710px) {
			margin: 0 0 60px;
		}
	}
}
