.insert {
	background: url('../images/main-page/insert-bg-one.png') no-repeat center/cover;

	@media only screen and (max-width: 420px) {
		background: url('../images/main-page/insert-bg-one-small.png') no-repeat center/cover;
	}

	&__content {
		display: flex;
		flex-direction: column;
		align-items: center;
		padding: 100px 0;

		* {
			color: $white;
		}
	}

	&__header {
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 0 0 60px;
	}

	&__body {
		max-width: 1188px;
		width: 100%;
	}

	&__text {
		font-size: 15px;
		line-height: 140%;
		font-weight: 400;
		text-align: center;
	}
}
.insert-two {
	background: url('../images/main-page/insert-bg-two.png') no-repeat center/cover;
	@media only screen and (max-width: 420px) {
		background: url('../images/main-page/insert-bg-two-small.png') no-repeat center/cover;
	}
}

.insert-about {
	background: url('../images/about/insert-about.png') no-repeat center/cover;
}
