.user {
	&__content {
		display: flex;
		flex-direction: column;
		align-items: center;
		@include aPx('padding-top', 100, 70, 1);
		@include aPx('padding-bottom', 100, 70, 1);
	}

	&__body {
		max-width: 1188px;
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	&__header {
		display: flex;
		justify-content: center;
		align-self: flex-start;
		@include aPx('margin-bottom', 60, 40, 1);
	}


	&__right {
		width: 100%;

		.form {
			width: 100%;
		}
	}

	&__inner {
		width: 100%;
		display: flex;
		gap: 20px;
		@media only screen and (max-width: 767px) {
			flex-direction: column;
			gap: 40px;
		}
	}

	&__subtitle {
		margin: 0 0 40px;
	}


	&__history-body {
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(433px, 1fr));
		gap: 20px;
		@media only screen and (max-width: 1280px) {
			grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
			gap: 10px;
		}
		@media only screen and (max-width: 1180px) {
			grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
		}
		@media only screen and (max-width: 1080px) {
			grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
		}
		@media only screen and (max-width: 370px) {
			grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
		}
	}

	&__history-item {
		height: 252px;
	}

	.sidebar {
		@media only screen and (max-width: 767px) {
			max-width: 100%;
		}
	}

	.sidebar__nav {
		width: 100%;
	}

	.sidebar__list {
		@media only screen and (max-width: 767px) {
			position: relative;
			height: 71px;
			&._show {
				height: auto;

				.sidebar__item {
					position: static;
					height: auto;
					opacity: 1;
					visibility: visible;
				}

				.sidebar__icon-arrow {
					opacity: 0;
				}
			}
		}
	}

	.sidebar__item {
		padding: 18px 45px 18px 24px;
		@media only screen and (max-width: 767px) {
			height: 0;
			position: absolute;
			opacity: 0;
			visibility: hidden;
			&._active {
				height: inherit;
				opacity: 1;
				visibility: visible;
			}
		}
	}
}

._form-user {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	@include aPx('padding-top', 60, 50, 1);
	@include aPx('padding-right', 80, 10, 1);
	@include aPx('padding-bottom', 60, 50, 1);
	@include aPx('padding-left', 80, 10, 1);
	@media only screen and (max-width: 767px) {
		align-items: center;
	}

	.form__item {
		&:not(:last-child) {
			margin: 0 0 15px;
		}
	}

	.form__button {
		margin: 0;
	}

	.form__body {
		@media only screen and (max-width: 768px) {
			flex-direction: column;
		}
	}

	.form__left {
		@media only screen and (max-width: 768px) {
			margin: 0;
		}

		.form__item {
			@media only screen and (max-width: 768px) {
				margin: 0 0 15px;
			}
		}
	}
}


.item-history {
	display: flex;
	background: $rgbaWhite;
	@include aPx('padding-top', 32, 26, 1);
	@include aPx('padding-right', 33, 10, 1);
	@include aPx('padding-bottom', 35, 22, 1);
	@include aPx('padding-left', 20, 10, 1);

	&__left {
		margin: 0 30px 0 0;
		@media only screen and (max-width: 1080px) {
			margin: 0 15px 0 0;
		}
	}

	&__right {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}

	&__inner {
		display: flex;
		flex-direction: column;
	}

	&__title {
		font-size: 15px;
		line-height: 140%;
		font-weight: 500;
		margin: 0 0 20px;
	}

	&__subtitle {
		font-size: 15px;
		line-height: 140%;
		font-weight: 400;
	}

	&__footer {
		width: 100%;
		display: flex;
		align-items: center;
	}

	&__date {
		flex: 1 1 auto;
		font-size: 15px;
		line-height: 140%;
		font-weight: 400;
		color: $rgbaGrey-three;
	}

	&__save {
		margin: 0 24px 0 0;

		&:before {
			font-size: 20px;
			font-weight: 700;
			color: $rgbaGrey-three;
			transition: color .4s ease 0s;
		}

		@media (any-hover: hover) {
			&:hover:before {
				color: rgba(0, 0, 0, 1);
			}
		}
	}

	&__link {
		&:before {
			font-size: 28px;
			color: $rgbaGrey-three;
			transition: color .4s ease 0s;
		}

		@media (any-hover: hover) {
			&:hover:before {
				color: rgba(0, 0, 0, 1);
			}
		}
	}
}

.sidebar__item-mobile {
	position: relative;
	width: 100%;
	background: $rgbaWhite;
	z-index: 1;
	@media only screen and (min-width: 767px) {
		display: none;
	}

	.sidebar__item-current {
		@extend .sidebar__item;

		.sidebar__arrow {
			&:before {
				font-size: 18px;
				transform: rotate(90deg);
			}
		}
	}
}

.sidebar__nav {
	@media only screen and (max-width: 768px) {
		position: relative;
		//&._active {
		//	.sidebar__list {
		//		height: max-content;
		//		opacity: 1;
		//		visibility: visible;
		//	}
		//	.sidebar__item-current {
		//		.sidebar__arrow {
		//			display: none;
		//		}
		//	}
		//}
	}
}

.sidebar__list {
	@media only screen and (max-width: 767px) {
		transition: opacity .2s ease 0s;
		.sidebar__arrow {
			display: none;
		}
	}
}

.sidebar__link {
	text-align: left;
}
