.our-production {
	background: $rgbaWhite;
	&__content {
		display: flex;
		flex-direction: column;
		align-items: center;
		@include aPx('padding-top', 100, 70, 1);
		@include aPx('padding-bottom', 100, 70, 1);
	}

	&__header {
		margin: 0 0 60px;
	}

	&__title {
	}

	&__body {
		max-width: 1188px;
		width: 100%;
	}

	&__text {
		font-size: 15px;
		line-height: 140%;
		font-weight: 400;
		text-align: center;
		@include aPx('margin-bottom', 80, 60, 1);
	}

	&__inner {
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
		gap: 20px;
		@include aPx('margin-bottom', 80, 40, 1);
		@media only screen and (max-width: 1280px) {
			grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
			gap: 10px;
		}
		@media only screen and (max-width: 992px) {
			grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
		}
		@media only screen and (max-width: 620px) {
			grid-template-columns: 1fr;
		}
	}

	&__card {
		height: 260px;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: space-between;
		background: $rgbaWhite-a;
		padding: 15px 15px 25px 15px;
		transition: all .4s ease 0s;
		@media (any-hover: hover) {
			&:hover {
				color: $white;
				background: $bgRed;
			}
		}
		@media only screen and (max-width: 1024px) {
			height: 220px;
		}
		@media only screen and (max-width: 768px) {
			height: 190px;
			padding: 15px 5px 25px 5px;
		}
		@media only screen and (max-width: 620px) {
			flex-direction: row;
			justify-content: flex-start;
			height: 130px;
			padding: 10px 15px;
		}
	}

	&__picture {
		width: 200px;
		height: 150px;
		display: flex;
		justify-content: center;
		align-items: center;

		&._imgFit .our-production__img {
			object-fit: contain;
		}

		@media only screen and (max-width: 992px) {
			width: 150px;
			height: 140px;
		}
		@media only screen and (max-width: 768px) {
			width: 110px;
			height: 100px;
		}
		@media only screen and (max-width: 620px) {
			width: 30%;
			margin: 0 10px 0 0;
		}
	}

	&__img {
	}

	&__note {
		display: inline-block;
		font-size: 20px;
		line-height: 140%;
		font-weight: 500;
		text-align: center;
		@media only screen and (max-width: 1024px) {
			line-height: 100%;
		}
		@media only screen and (max-width: 768px) {
			font-size: 16px;
		}
		@media only screen and (max-width: 620px) {
			width: 70%;
			font-size: 20px;
			text-align: left;
		}
	}

	&__footer {
	}

	&__btn {
	}
}
