.modal.modal-login-password-restore {
	&:before {
		background: $rgbaBgModal;
	}

	.modal__content {
		justify-content: space-between;
	}

	.modal__body {
		max-width: 800px;
		width: 100%;
		flex-direction: column;
		align-items: center;
		border-radius: 20px;
		background: $rgbaWhite;
		@include aPx('padding-top', 57, 33, 1);
		@include aPx('padding-right', 34, 30, 1);
		@include aPx('padding-bottom', 77, 38, 1);
		@include aPx('padding-left', 34, 30, 1);

		@media only screen and (max-width: 360px) {
			padding-right: 10px;
			padding-left: 10px;
		}
		@media only screen and (max-width: 768px) {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
		}
	}

	.modal__register-form {
		max-width: 350px;
		width: 100%;
	}

	.form__item {
		&:not(:last-child) {
			margin: 0 0 15px;
		}

		&:nth-child(2) {
			margin: 0 0 40px;
		}
	}
}

