.certificate {

	&__content {
		display: flex;
		flex-direction: column;
		align-items: center;
		@include aPx('padding-top', 100, 70, 1);
		@include aPx('padding-bottom', 100, 70, 1);
	}

	&__header {
		display: flex;
		justify-content: center;
		align-items: center;
		@include aPx('margin-bottom', 70, 47, 1);
	}

	&__title {
		text-align: center;
	}

	&__body {
		max-width: 1188px;
		width: 100%;
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(584px, 1fr));
		gap: 20px;
		@media only screen and (max-width: 1280px) {
			grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
		}
		@media only screen and (max-width: 1080px) {
			grid-template-columns: repeat(auto-fit, minmax(450px, 1fr));
		}
		@media only screen and (max-width: 992px) {
			grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
		}
		@media only screen and (max-width: 420px) {
			grid-template-columns: 1fr;
		}
	}

	&__card {
		position: relative;
		display: flex;
		border: 1px solid $rgbaWhite;
		background: $rgbaWhite;
		@include aPx('padding-top', 35, 35, 1);
		@include aPx('padding-right', 35, 30, 1);
		@include aPx('padding-bottom', 35, 35, 1);
		@include aPx('padding-left', 35, 30, 1);
		transition: all .4s ease 0s;

		&:nth-child(4) {
			.certificate__card-body .certificate__text {
				&:nth-child(3) {
					margin: 0 0 25px;
				}
			}
		}

		@media (any-hover: hover) {
			&:hover {
				border-color: $black;
				transform: translateY(-4px);
			}
		}
		@media only screen and (max-width: 420px) {
			flex-direction: column;
			align-items: center;
		}
	}

	&__card-left {
		@include aPx('margin-right', 32, 15, 1);
		@media only screen and (max-width: 420px) {
			margin: 0 0 20px;
		}
	}

	&__slider {
		@include adaptive(width, 1024px, 1920px, 174px, 220px);
		@include adaptive(height, 1024px, 1920px, 247px, 313px);
		overflow: hidden;
		@media only screen and (max-width: 420px) {
			max-width: 220px;
			width: 100%;
			height: 313px;
		}
	}

	&__card-right {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		gap: 40px;
	}

	&__subtitle {
		font-size: 15px;
		line-height: 140%;
		font-weight: 500;
		@include aPx('margin-bottom', 40, 20, 1);
		@media only screen and (max-width: 1024px) {
			font-size: 13px;
		}
	}

	&__text {
		font-size: 15px;
		line-height: 140%;
		font-weight: 400;

		&:nth-child(2) {
			margin: 0 0 25px;
		}

		@media only screen and (max-width: 1024px) {
			font-size: 13px;
		}
	}

	&__card-footer {
		display: flex;
		align-items: center;
		justify-content: space-between;
		@media only screen and (max-width: 420px) {
			position: absolute;
			top: 35px;
			left: 0;
			right: 0;
			width: 75%;
			justify-content: space-between;
			margin-inline: auto;
		}
		@media only screen and (max-width: 380px) {
			width: 80%;
		}
		@media only screen and (max-width: 370px) {
			width: 93%;
		}
	}

	&__counter {
		display: flex;
		align-items: center;
	}

	&__counter-separator,
	&__counter-current,
	&__counter-tottal {
		font-size: 15px;
		line-height: 140%;
		font-weight: 400;
	}

	&__counter-separator {
		padding: 0 2px;
	}

	&__link {
		&:before {
			font-size: 28px;
			font-weight: 700;
			color: rgba(0, 0, 0, 0.5);
			transition: all .4s ease 0s;
		}

		@media (any-hover: hover) {
			&:hover:before {
				color: rgba(0, 0, 0, 1);
			}
		}
	}
}
