.modal.modal-login-two {
	&:before {
		background: $rgbaBgModal;
	}

	.modal__content {
		justify-content: space-between;
		padding-top: 10px;
	}

	.modal__header {
		margin: 0 0 10px;
	}

	.modal__register-note {
		max-width: 390px;
		width: 100%;
	}

	.modal__body {
		max-width: 800px;
		width: 100%;
		@media only screen and (max-width: 360px) {
			padding-right: 10px;
			padding-left: 10px;
		}
		@media only screen and (max-width: 768px) {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
		}
	}

	.modal__register-form {
		max-width: 350px;
		width: 100%;
	}

	.modal__register-inner {
		width: 100%;
	}

	.form__item {
		&:not(:last-child) {
			margin: 0 0 15px;
		}

		&:nth-child(2) {
			margin: 0 0 40px;
		}
	}

	.modal__register-footer {
		.modal__register-link:nth-child(2) {
			display: inline-block;
			margin: 0 0 5px;
		}
	}
}
