.contacts {

	span,
	b,
	a {
		display: inline-block;
	}

	a {
		transition: color .4s ease 0s;
		@media (any-hover: hover) {
			&:hover {
				color: rgba(255, 0, 0, 0.88);
			}
		}
	}

	&__bg-image {
		height: 396px;
		@media only screen and (max-width: 767px) {
			height: 457px;
		}
	}

	&__content {
		display: flex;
		flex-direction: column;
		align-items: center;
		@include aPx('padding-top', 100, 70, 1);
		@include aPx('padding-bottom', 100, 70, 1);
	}

	&__header {
		display: flex;
		justify-content: center;
		align-items: center;
		@include aPx('margin-bottom', 70, 50, 1);
	}

	&__body {
		max-width: 1188px;
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		background: $rgbaWhite;
		@include aPx('padding-top', 50, 50, 1);
		@include aPx('padding-right', 47, 10, 1);
		@include aPx('padding-bottom', 50, 50, 1);
		@include aPx('padding-left', 47, 10, 1);
	}

	&__left {
		@media only screen and (max-width: 1160px) {
			margin: 0 20px 0 0;
		}
		@media only screen and (max-width: 767px) {
			margin: 0;
		}
	}

	&__body-office {
		width: 100%;
		display: flex;
		justify-content: space-between;
		@media only screen and (max-width: 767px) {
			flex-direction: column;
		}
	}

	&__body-warehouse {
		width: 100%;
		display: flex;
		justify-content: space-between;
		@media only screen and (max-width: 767px) {
			flex-direction: column;
		}
	}

	&__subtitle {
		@include aPx('margin-bottom', 47, 40, 1);
	}

	&__left_office {
		max-width: 412px;
		width: 100%;
		display: flex;
		flex-direction: column;
		@media only screen and (max-width: 767px) {
			margin: 0 0 30px;
		}
	}

	&__left_warehouse {
		max-width: 490px;
		width: 100%;
		display: flex;
		flex-direction: column;
		@media only screen and (max-width: 767px) {
			margin: 0 0 30px;
		}
	}

	&__right_warehouse {
		max-width: 497px;
		width: 100%;
		display: flex;
		flex-direction: column;
		span,p {
			font-size: 15px;
			line-height: 28px;
			font-weight: 400;
		}
		b:nth-child(4) {
			margin: 0 0 30px;
		}
		b:nth-child(7) {
			margin: 0 0 30px;
		}
		span:nth-child(12) {
			margin: 0 0 30px;
		}
	}

	&__subtitle-address {
		font-size: 20px;
		line-height: 140%;
		font-weight: 500;
		margin: 0 0 15px;
	}

	&__address {
		font-size: 15px;
		line-height: 140%;
		font-weight: 400;
		margin: 0 0 40px;
	}

	&__phone-title {
		font-size: 20px;
		line-height: 140%;
		font-weight: 500;
		margin: 0 0 15px;
	}

	&__phone {
		font-size: 15px;
		line-height: 28px;
		font-weight: 0;
		&:nth-child(5) {
			margin: 0 0 30px;
		}
	}

	&__phone-text {
		font-size: 15px;
		line-height: 28px;
		font-weight: 400;
		margin: 0 0 0 5px;
	}

	&__email-title {
		font-size: 20px;
		line-height: 140%;
		font-weight: 500;
		margin: 0 0 15px;
	}

	&__email-items {
		display: flex;
		gap: 30px;
		margin: 0 0 30px;
		@media only screen and (max-width: 380px) {
			flex-direction: column;
		}
	}

	&__email-item {
		display: flex;
		flex-direction: column;
	}

	&__email-note {
		font-size: 15px;
		line-height: 21px;
		font-weight: 400;
	}

	&__email {
		font-size: 15px;
		line-height: 21px;
		font-weight: 500;
	}

	&__sales {
		font-size: 20px;
		line-height: 140%;
		font-weight: 500;
		margin: 0 0 15px;
	}

	&__sales-note {
		font-size: 15px;
		line-height: 26px;
		font-weight: 400;
	}

	&__sales-phone {
		font-size: 15px;
		line-height: 26px;
		font-weight: 500;
	}

	&__sales-email {
		font-size: 15px;
		line-height: 26px;
		font-weight: 500;
	}

	&__right {
		max-width: 490px;
		width: 100%;
	}

	&__picture-map {
		@include aPx('margin-bottom', 40, 15, 1);
		img {
			transition: transform .4s ease 0s;
		}
		@media (any-hover: hover) {
			&:hover {
				img {
					transform: scale(1.3);
				}
			}
		}
	}

	&__items {
		display: flex;
		justify-content: space-between;
		@media only screen and (max-width: 768px) {
			display: grid;
			grid-template-columns: 1fr 1fr;
			grid-template-rows: 1fr;
		}
	}

	&__item {
		display: flex;
		flex-direction: column;
		&:nth-child(1) {
			grid-column: 1/-1;
			grid-row: 2 / 3;
		}
		&:nth-child(2) {
			width: fit-content;
			justify-self: flex-end;
			grid-column: 1/-1;
			grid-row: 1 / 2;
			margin: 0 0 30px;
		}
	}

	&__schedule {
		font-size: 20px;
		line-height: 140%;
		font-weight: 500;
		margin: 0 0 15px;
	}

	&__days {
		max-width: 180px;
		display: flex !important;
		justify-content: space-between;
		flex: 1;
		font-size: 15px;
		line-height: 163.5%;
		font-weight: 400;
	}

	&__time {
		flex: 1;
		max-width: 100px;
		font-size: 15px;
		line-height: 163.5%;
		font-weight: 500;
		text-align: left;
	}

	&__link {
		position: relative;
		font-size: 15px;
		line-height: 140%;
		font-weight: 400;
		&:before {
			position: absolute;
			content: '';
			left: 0;
			bottom: 0;
			width: 100%;
			height: 1px;
			background: $black;
			z-index: 0;
			transition: opacity .4s ease 0s;
		}

		@media (any-hover: hover) {
			&:hover:before {
				opacity: 0;
				visibility: hidden;
			}
		}
	}

	&__subtitle-requisites {
		font-size: 20px;
		line-height: 140%;
		font-weight: 500;
		margin: 0 0 15px;
	}

	&__ooo {
		font-size: 15px;
		line-height: 28px;
		font-weight: 500;
	}

	&__requisites-address {
		font-size: 15px;
		line-height: 28px;
		font-weight: 500;
		&:nth-child(5) {
			margin: 0 0 15px;
		}
		span {
			display: inline;
			font-size: 15px;
			line-height: 28px;
			font-weight: 400;
			white-space: normal;
			margin: 0 0 0 10px;
		}
	}

	&__requisites-item {
		font-size: 15px;
		line-height: 28px;
		font-weight: 500;
		span {
			font-size: 15px;
			line-height: 28px;
			font-weight: 400;
			margin: 0 0 0 10px;
		}
	}

	&__invoices {
		font-size: 20px;
		line-height: 140%;
		font-weight: 500;
		margin: 0 0 15px;
	}

	&__invoices-requisites {
		font-size: 15px;
		line-height: 28px;
		font-weight: 500;
		span {
			margin: 0 0 0 10px;
		}
	}
}
