@use "sass:math";
@import 'vars';
@import 'mixins';
@import 'reset';
@import 'fonts';
@import 'icons';

body {
	&._lock {
		overflow: hidden;
	}
}

.wrapper {
	width: 100%;
	min-height: 100%;
	display: flex;
	flex-direction: column;
	overflow: hidden;
}

.page {
	flex: 1 1 auto;
	margin: 112px 0 0 0;
}

@if ($responsiveType==1) {
	[class*='__container'] {
		@if ($maxWidthContainer>0) {
			max-width: rem($maxWidthContainer);
			-webkit-box-sizing: content-box;
			box-sizing: content-box;
			margin: 0 auto;
		}
		@if ($containerPadding>0) {
			padding: 0 rem(math.div($containerPadding, 2));
		}
		@media only screen and (max-width: 380px) {
			padding: 0 10px;
		}
	}
} @else {
	[class*='__container'] {
		margin: 0 auto;
		@if ($maxWidthContainer>0) {
			max-width: rem($maxWidthContainer);
			-webkit-box-sizing: content-box;
			box-sizing: content-box;
		} @else {
			@if ($containerPadding>0) {
				padding: 0 rem(math.div($containerPadding, 2));
			}
		}
		@media (max-width: $pc) {
			max-width: rem(970);
		}
		@media (max-width: $tablet) {
			max-width: rem(750);
		}
		@media (max-width: $mobile) {
			max-width: none;
			@if ($containerPadding>0 and $maxWidthContainer>0) {
				padding: 0 rem(math.div($containerPadding, 2));
			}
		}
	}
}

@import 'commons';
