.modal.modal-login-thanks {
	&:before {
		background: $rgbaBgModal;
	}

	.modal__content {
		justify-content: space-between;
	}

	.modal__body {
		max-width: 800px;
		width: 100%;
	}

	.modal__register-title {
		margin: 0 0 40px;
	}

	.modal__register-note {
		max-width: 400px;
		width: 100%;
		margin: 0 0 60px;
	}
}
