.footer {

	width: 100%;
	background: $rgbaGrey;

	&__content {
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		@include aPx('padding-top', 44, 45, 1);
		@include aPx('padding-bottom', 56, 50, 1);
	}

	&__header {
		@include aPx('margin-bottom', 50, 56, 1);
	}

	&__body {
		width: 100%;
		display: grid;
		grid-template-columns: 50% 50%;
		grid-template-rows: 1fr;
		@media (max-width: 1366px) {
			grid-template-columns: 55% 45%;
		}
		@media (max-width: 1280px) {
			grid-template-columns: 70% 30%;
		}
		@media (max-width: 1080px) {
			grid-template-columns: 67% 33%;
		}
		@media (max-width: 1024px) {
			grid-template-columns: 66% 38%;
		}
		@media (max-width: 992px) {
			grid-template-columns: 1fr;
			grid-template-rows: repeat(2, 1fr);
		}
	}

	&__body-left {
		@media (max-width: 992px) {
			margin: 0 0 25px;
		}

		@media (max-width: 650px) {
			margin: 0 0 44px;
		}
	}

	&__list {
		display: grid;
		grid-template-columns: repeat(5, 1fr);
		row-gap: 38px;
		@media (max-width: 1024px) {
			grid-template-columns: repeat(4, 1fr);
			grid-template-rows: repeat(5, 1fr);
		}
		@media (max-width: 992px) {
			grid-template-columns: repeat(5, 1fr);
			grid-template-rows: repeat(1, 1fr);
		}
		@media (max-width: 650px) {
			grid-template-columns: repeat(2, 1fr);
			grid-template-rows: repeat(5, 1fr);
			column-gap: 70px;
			row-gap: 34px;
		}
	}

	&__item {
		padding: 0 5px;

		&:last-child {
			grid-column: 4 / 6;
			@media (max-width: 1024px) {
				grid-column: 1 / -1;
				grid-row: 5 / 6;
			}
			@media (max-width: 992px) {
				grid-column: 4 / 6;
				grid-row: unset;
			}
		}

		@media (max-width: 1024px) {
			&:nth-child(5) {
				grid-row: 3 / 4;
			}

			&:nth-child(6) {
				grid-row: 3 / 4;
			}

			&:nth-child(7) {
				grid-row: 3 / 4;
			}

			&:nth-child(8) {
				grid-row: 3 / 4;
			}
		}
		@media (max-width: 992px) {
			&:nth-child(5) {
				grid-row: unset;
			}

			&:nth-child(6) {
				grid-row: unset;
			}

			&:nth-child(7) {
				grid-row: unset;
			}

			&:nth-child(8) {
				grid-row: unset;
			}
		}
		@media (max-width: 650px) {
			padding: 0;
			&:nth-child(3) {
				grid-column: 1 / 2;
			}
			&:nth-child(5) {
				grid-column: 1 / 2;
			}
			&:nth-child(7) {
				grid-column: 1 / 2;
			}
			&:nth-child(9) {
				grid-column: 1 / -1;
			}
		}
	}

	&__link {
		position: relative;
		@include aFont(13, 15, 1);
		font-weight: normal;
		white-space: nowrap;
		color: $white;

		&:before {
			position: absolute;
			content: '';
			left: 0;
			bottom: -2px;
			width: 0%;
			height: 1px;
			background: $white;
			z-index: 0;
			transition: width 500ms cubic-bezier(0.000, 0.000, 0.220, 1.055);
			animation-timing-function: cubic-bezier(0.000, 0.000, 0.220, 1.055);
		}

		@media (any-hover: hover) {
			&:hover:before {
				width: 100%;
			}
		}
	}

	&__body-right {
		display: grid;
		grid-template-columns: 50% 50%;
		grid-template-rows: 1fr;
		@media (max-width: 1366px) {
			grid-template-columns: 45% 60%;
		}
		@media (max-width: 1280px) {
			grid-template-rows: 2fr;
		}
		@media (max-width: 1024px) {
			grid-template-rows: 1fr 1fr;
			row-gap: 22px;
		}
		@media (max-width: 992px) {
			grid-template-columns: 50% 62%;
			grid-template-rows: 1fr;
		}

		@media (max-width: 650px) {
			grid-template-columns: 1fr;
			grid-template-rows: repeat(2, 1fr);
			row-gap: unset;
		}
	}

	&__copy {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 13px;
		line-height: 140%;
		border-left: 1px solid $white;
		color: $white;
		@media (max-width: 1280px) {
			grid-column: 1 / -1;
		}
		@media (max-width: 1024px) {
			grid-column: 1 / -1;
			grid-row: 1 / 2;
		}
		@media (max-width: 992px) {
			grid-column: 1 / 2;
			grid-row: unset;
			border: none;
		}
		@media (max-width: 650px) {
			grid-column: 1 / -1;
			grid-row: unset;
			justify-content: flex-start;
			border: none;
			&:before {
				position: absolute;
				content: '';
				top: 0;
				left: 0;
				width: 70px;
				height: 1px;
				background: $white;
			}
		}
	}

	&__copy-text {
		@media (max-width: 1280px) {
			max-width: 260px;
			width: 100%;
			margin: 0 0 8px;
		}
	}

	&__info {
		position: relative;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		border-left: 1px solid $white;
		@media (max-width: 1280px) {
			grid-column: 1 / -1;
			grid-row: 2 / 3;
		}
		@media (max-width: 1024px) {
			grid-column: 1 / -1;
			grid-row: 2 / 3;
		}

		@media (max-width: 992px) {
			grid-column: 2 / 3;
			grid-row: unset;
		}

		@media (max-width: 650px) {
			grid-column: 1 / -1;
			grid-row: unset;
			align-items: flex-start;
			border: none;
			&:before {
				position: absolute;
				content: '';
				top: 0;
				left: 0;
				width: 70px;
				height: 1px;
				background: $white;
			}
		}
		@media (max-width: 350px) {
			padding: 28px 0 0 0;
		}
	}

	&__info-item {
		max-width: 260px;
		width: 100%;
		display: flex;
		align-items: center;

		&:not(:last-child) {
			margin: 0 0 15px;
			@media (max-width: 1280px) {
				margin: 0 0 8px;
			}
			@media (max-width: 650px) {
				margin: 0 0 15px;
			}
		}
		@media (max-width: 650px) {
			max-width: 100%;
		}
		@media (max-width: 350px) {
			flex-direction: column;
			align-items: flex-start;
		}
	}

	&__info-tel {
		font-size: 13px;
	}

	&__info-email._phone-phone {
		margin: 0;
	}
}

