    margin: 0 0 25px;
  }
}
@media only screen and (max-width: 767px) {
  .product__items-item {
    justify-content: center;
  }
  .product__items-item:not(:last-child) {
    margin: 0 0 18px;
  }
}
.product__items-picture {
  position: relative;
  width: 56px;
  height: 56px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 18px 0 0;
}
.product__items-item-link {
  position: relative;
  display: inline-block;
  font-size: 15px;
  line-height: 19px;
  font-weight: 500;
  transition: color 0.4s ease 0s;
}
.product__items-item-link:before {
  position: absolute;
  content: "";
  left: 0;
  bottom: -1px;
  width: 100%;
  height: 1px;
  background: #000000;
  z-index: 0;
  transition: background 0.4s ease 0s;
}
@media (any-hover: hover) {
  .product__items-item-link:hover {
    color: rgba(0, 0, 0, 0.5);
  }
  .product__items-item-link:hover:before {
    background: rgba(0, 0, 0, 0.5);
  }
}
.product__items-icon {
  width: 48px;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: 1px solid rgb(239, 64, 54);
  margin: 0 18px 0 0;
}
.product__items-icon:before {
  font-size: 24px;
  color: #EF4036;
}
.product__body {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid rgb(239, 64, 54);
  padding-bottom: 80px;
  margin-bottom: 60px;
}
@media (max-width: 1660px) {
  .product__body {
    padding-bottom: calc(70px + 10 * ((100vw - 320px) / 1340));
  }
}
@media (max-width: 1660px) {
  .product__body {
    margin-bottom: calc(40px + 20 * ((100vw - 320px) / 1340));
  }
}
.product__body > .product__body-subtitle {
  margin: 0 0 15px;
}
.product__body-title {
  margin: 0 0 40px;
}
.product__inner {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin: 0 0 60px;
}
.product__inner-top {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  margin: 0 0 40px;
}
@media only screen and (max-width: 767px) {
  .product__inner-top {
    grid-template-columns: 1fr;
  }
}
.product__inner-top-left {
  display: flex;
  flex-direction: column;
  gap: 25px;
}
.product__inner-body {
  display: flex;
  gap: 20px;
}
@media only screen and (max-width: 767px) {
  .product__inner-body {
    flex-direction: column;
  }
}
.product__left {
  flex: 1;
}
.product__text {
  font-size: 15px;
  line-height: 140%;
  font-weight: 400;
}
.product__left-item {
  display: flex;
  flex-direction: column;
}
.product__left-item:not(:last-child) {
  margin: 0 0 25px;
}
.product__body-subtitle {
  display: inline-block;
  font-size: 15px;
  line-height: 140%;
  font-weight: 500;
  text-transform: uppercase;
}
.product__note {
  font-size: 15px;
  line-height: 28px;
  font-weight: 400;
}
.product__right {
  flex: 1;
}
.product__right .product__text:nth-child(1) {
  margin: 0 0 25px;
}
.product__table {
  width: 100%;
  margin: 0 0 10px;
}
.product__body-note {
  font-weight: 400;
  font-size: 12px;
  line-height: 140%;
}
.product__filter {
  margin: 0 0 60px;
}
.product__filter .references__item._active .references__btn {
  border-color: rgb(239, 64, 54);
}
.product__filter .references__item._active .references__btn ~ .references__btn-icon {
  opacity: 1;
  visibility: visible;
}
.product__tab {
  display: none;
}
.product__tab._active {
  display: block;
}
.product__tab-title {
  display: inline-block;
  font-size: 15px;
  line-height: 140%;
  font-weight: 500;
  text-transform: uppercase;
  margin: 0 0 15px;
}
.product__tab-picure {
  width: 585px;
  height: 486px;
  background: rgb(255, 255, 255);
  margin: 0 0 40px;
  height: 300px;
}
@media (min-width: 320px) {
  .product__tab-picure {
    height: calc(300px + 186 * (100vw - 320px) / 447);
  }
}
@media (min-width: 767px) {
  .product__tab-picure {
    height: 486px;
  }
}
@media only screen and (max-width: 767px) {
  .product__tab-picure {
    width: 100%;
  }
}
.product__tab-img {
  object-fit: contain !important;
}
.product__tab-table {
  width: 100%;
}
@media only screen and (max-width: 767px) {
  .product__tab-overflow {
    width: 100%;
    overflow-x: auto;
  }
}

.table__product-header th.table__product-col {
  background: rgb(239, 64, 54);
  padding: 10px 5px;
}
.table__product-col {
  text-align: center;
  background: rgb(255, 255, 255);
  padding: 15px 5px;
}
.table__product-col b.table__product-text {
  font-weight: 500;
}
.table__product-title {
  font-size: 15px;
  line-height: 140%;
  font-weight: 500;
  color: #FFFFFF;
}
.table__product-text {
  font-size: 15px;
  line-height: 19px;
  font-weight: 400;
}
.table__tab {
  width: 100%;
}
.table__tab-header th.table__tab-col {
  background: rgb(239, 64, 54);
  padding: 10px 26px;
}
.table__tab-col {
  text-align: center;
  background: rgb(255, 255, 255);
  padding: 15px 26px;
}
.table__tab-title {
  font-size: 15px;
  line-height: 140%;
  font-weight: 500;
  color: #FFFFFF;
}
.table__tab-body .table__tab-row:nth-child(1) {
  font-weight: 400;
}
.table__tab-text {
  white-space: nowrap;
}