.clients {
	background: $rgbaWhite;
	&__content {
		display: flex;
		flex-direction: column;
		align-items: center;
		@include aPx('padding-top', 100, 70, 1);
		@include aPx('padding-bottom', 100, 70, 1);
	}
	&__header {
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 0 0 60px;
	}
	&__title {
		text-align: center;
	}
	&__body {
		max-width: 1188px;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: 10px;
		margin: 0 0 80px;
		@media only screen and (max-width: 620px) {
			flex-direction: column;
			row-gap: 20px;
		}
	}
	&__item {
	}
	&__img {
	}
	&__footer {
		display: flex;
		justify-content: center;
		align-items: center;
	}
	&__btn {
	}
}
