.form {
	width: 330px;
	display: flex;
	flex-direction: column;
	align-items: center;
	background: $rgbaWhite;
	@include aPx('padding-top', 30, 25, 1);
	@include aPx('padding-right', 42, 5, 1);
	@include aPx('padding-bottom', 42, 25, 1);
	@include aPx('padding-left', 42, 5, 1);
	@media only screen and (max-width: 420px) {
		width: 100%;
	}
	&__header {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin: 0 0 27px;
	}

	&__title {
		display: inline-block;
		font-size: 20px;
		line-height: 25px;
		font-weight: 500;
		margin: 0 0 15px;
	}

	&__subtitle {
		font-size: 15px;
		line-height: 19px;
		font-weight: 400;
		text-align: center;
		margin: 0 0 15px;
	}

	&__item {
		width: 100%;
		height: 50px;
		&:nth-child(2) {
			margin: 0 0 15px;
		}
		&:nth-child(3) {
			margin: 0 0 25px;
		}
	}

	&__label {
	}

	&__input {
		width: 100%;
		height: 100%;
		padding: 15px 12px;
	}

	&__button {
		margin: 0 0 15px;
	}

	&__footer {
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	&__text {
		line-height: 19px;
		font-weight: 400;
	}

	&__link {
		position: relative;
		font-size: 15px;
		line-height: 19px;
		font-weight: 500;
		text-align: center;
		transition: color .4s ease 0s;
		&:before {
			position: absolute;
			content: '';
			left: 0;
			bottom: -2px;
			width: 100%;
			height: 1px;
			background: $black;
			z-index: 0;
			transition: background .4s ease 0s;
		}
		@media (any-hover: hover) {
			&:hover {
				color: rgba(0, 0, 0, 0.5);
				&:before {
					background: rgba(0, 0, 0, 0.5);
				}
			}
		}
	}
}
