.reviews {
	background: $rgbaWhite-a;

	&__content {
		display: flex;
		flex-direction: column;
		align-items: center;
		@include aPx('padding-top', 91, 61, 1);
		@include aPx('padding-bottom', 100, 77, 1);
	}

	&__header {
		margin: 0 0 60px;
	}

	&__body {
		max-width: 1188px;
		width: 100%;
		display: flex;
		justify-content: space-between;
		gap: 20px;
		@include aPx('margin-bottom', 80, 40, 1);
	}

	&__card {
		background: $rgbaWhite;
	}
}

.card {
	position: relative;
	min-height: 496px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	border: 1px solid $rgbaWhite;
	padding: 30px;
	transition: all .4s ease 0s;
	@media (any-hover: hover) {
		&:hover {
			border-color: $black;
			transform: translateY(-4px);
		}
	}
	@media only screen and (max-width: 992px) {
		&:nth-child(3) {
			display: none;
		}
	}
	@media only screen and (max-width: 620px) {
		&:nth-child(2) {
			display: none;
		}
	}

	&__body {
		display: flex;
		flex-direction: column;
	}

	&__title {
		font-size: 20px;
		line-height: 140%;
		font-weight: 500;
		margin: 0 0 15px;
	}

	&__subtitle {
		font-size: 15px;
		line-height: 140%;
		font-weight: 500;
		margin: 0 0 15px;
	}

	&__text {
		font-size: 15px;
		line-height: 140%;
		font-weight: 400;
	}

	&__footer {
		display: flex;
		flex-direction: column;
	}

	&__pos {
	}

	&__name {
		margin: 0 0 20px;
	}

	&__date {
	}
	.card__pos,
	.card__name,
	.card__date {
		font-size: 15px;
		line-height: 140%;
		font-weight: 400;
	}
	&__icon {
		position: absolute;;
		right: 30px;
		bottom: 30px;
		&:before {
			color: $red;
		}
	}
}
