.modal.modal-login-password {
	&:before {
		background: $rgbaBgModal;
	}

	.modal__content {
		//justify-content: space-between;
	}

	.modal__body {
		max-width: 800px;
		width: 100%;
		flex-direction: column;
		align-items: center;
		border-radius: 20px;
		background: $rgbaWhite;
		@include aPx('padding-top', 57, 33, 1);
		@include aPx('padding-right', 34, 30, 1);
		@include aPx('padding-bottom', 77, 38, 1);
		@include aPx('padding-left', 34, 30, 1);

		@media only screen and (max-width: 360px) {
			padding-right: 10px;
			padding-left: 10px;
		}
		@media only screen and (max-width: 768px) {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
		}
	}

	.modal__register-title {
		margin: 0 0 40px;
	}

	.modal__register-note {
		max-width: 400px;
		width: 100%;
		margin: 0 0 60px;
	}

	.modal__register-form {
		max-width: 350px;
		width: 100%;
	}

	.form__item {
		width: 100%;
		margin: 0 0 40px;
	}

	.modal__register-footer {
		width: 100%;
		display: flex;
		align-items: center;
		flex-direction: row;
		justify-content: space-between;
		button {
			margin: 0;
		}
		.modal__register-btn._btn-border {
			width: 160px;
			transition: all 0.4s ease 0s;

			@media (any-hover: hover) {
				&:hover {
					border-color: rgba(0, 0, 0, 0.33);
					background: $rgbaBgModal;
				}
			}
		}
	}
}
