._big-title {
	@include aFont(90, 50, 1);
	line-height: 100%;
	font-weight: 700;
	color: $white;
}

._title {
	@include aFont(35, 22, 1);
	line-height: 140%;
	font-weight: 700;
	color: $black;
}

._subtitle {
	@include aFont(25, 20, 1);
	font-weight: 500;
	@include aPx('line-height', 31, 20, 1);
	color: $black;
}

._text {
	line-height: 140%;
}

._btn-header {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 13px;
	@include aFont(13, 15, 1);
	line-height: 16px;
	font-weight: 700;
	white-space: nowrap;
	color: $white;
	border-radius: 30px;
	overflow: hidden;
	background: $bgRed;
	padding: 10px 17px;
	transition: background .4s ease 0s;
	@media (any-hover: hover) {
		&:hover {
			background: $rgbaGrey;
		}
	}
}

._btn-red {
	width: var(--btn-red-width);
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 15px;
	line-height: 19px;
	font-weight: 700;
	border-radius: 30px;
	color: $white;
	background: $bgRed;
	@include aPx('padding-top', 16, 16, 1);
	@include aPx('padding-bottom', 15, 15, 1);
	transition: background .4s ease 0s;
	@media (any-hover: hover) {
		&:hover {
			background: $rgbaGrey;
		}
	}
	@media only screen and (max-width: 390px) {
		width: 290px;
	}
}

._btn-border {
	width: 262px;
	display: flex;
	justify-content: center;
	align-items: center;
	line-height: 19px;
	font-weight: 500;
	border: 1px solid $black;
	border-radius: 30px;
	color: $black;
	@include aPx('padding-top', 16, 16, 1);
	@include aPx('padding-bottom', 15, 15, 1);
	transition: border-color .4s ease 0s;
	@media (any-hover: hover) {
		&:hover {
			border-color: $bgRed;
		}
	}
}

._imgFit {
	position: relative;

	img {
		position: absolute;
		content: '';
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		display: block;
		object-fit: cover;
	}
}

._img {
	display: block;
	max-width: 100%;
	width: 100%;
}

._br {
	border-radius: 10px;
}

._border {
	border: 1px solid $borderColor;
}

._grid {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(var(--grid-width), 1fr));
}

._notMb {
	&:not(:last-child) {
		margin: 0 0 var(--margin-bottom) 0;
	}
}

._notMr {
	&:not(:last-child) {
		margin: 0 var(--margin-right) 0 0;
	}
}

._font500-20-25 {
	font-size: 20px;
	line-height: 25px;
	font-weight: 500;
}


/*--------------------------------------------------Header--------------------------------------------------*/

._phone-icon {
	&:before {
		color: $white;
	}

	margin: 0 16px 0 0;
	@media (max-width: 620px) {
		&:before {
			font-size: 22px;
		}
		margin: 0 26px 0 0;
	}
}

._phone-phone {
	font-weight: 700;
	color: $white;
	margin: 0 8px 0 0;
	transition: color .4s ease 0s;
	@media (any-hover: hover) {
		&:hover {
			color: rgba(255, 0, 0, 0.88);
		}
	}

	//@media (max-width: 620px) {
	//	font-size: 20px;
	//	margin: 0 0 10px;
	//}
}

._phone-text {
	font-size: 13px;
	font-weight: 400;
	color: $white;
	@media (max-width: 620px) {
		font-size: 15px;
	}
}

._user {
	position: relative;
	@include aFont(13, 15, 1);
	line-height: 16px;
	font-weight: 500;
	margin: 0 20px 0 0;
	transition: color .4s ease 0s;

	&:before {
		position: absolute;
		content: '';
		left: 0;
		bottom: -1px;
		width: 100%;
		height: 1px;
		background: $black;
		z-index: 0;
		transition: background .4s ease 0s;
	}
	@media (any-hover: hover) {
		&:hover {
			color: rgba(0, 0, 0, 0.5);
			&:before {
				background: rgba(0, 0, 0, 0.5);
			}
		}
	}
}

._not-registered,
._registered {
	display: flex;
	align-items: center;
}

.not-registered {
	._registered {
		display: none;
	}
}

.registered {
	._not-registered {
		display: none;
	}
}

