/*-------------------------------------------------------------404-------------------------------------------------------------*/


.not-found {
	&__content {
		display: grid;
		grid-template-columns: 1fr;
		grid-template-rows: min-content 1fr 1fr 1fr min-content;
		justify-content: center;
		@media only screen and (max-width: 620px) {
			padding: 50px 0 182px;
		}
	}

	&__header {
		display: flex;
		justify-content: center;
		align-items: center;
		@media only screen and (max-width: 620px) {
			margin: 0 0 60px;
		}
	}

	&__title {
		font-size: 25px;
		line-height: 140%;
		font-weight: 400;
		text-align: center;
		@media only screen and (max-width: 350px) {
			font-size: 20px;
			line-height: 1;
		}
	}

	&__body {
		display: flex;
		flex-direction: column;
		align-items: center;
		@media only screen and (max-width: 620px) {
			margin: 0 0 60px;
		}
	}

	&__footer {
		display: flex;
		justify-content: center;
		align-items: center;
	}
}

/*--------------------------------------------------------POLICY--------------------------------------------------------*/


.policy {
	* {
		a {
			text-decoration: underline;
		}
	}

	&__content {
		display: flex;
		flex-direction: column;
		align-items: center;
		@include aPx('padding-top', 100, 70, 1);
		@include aPx('padding-bottom', 100, 70, 1);
	}

	&__header {
		display: flex;
		justify-content: center;
		align-items: center;
		@include aPx('margin-bottom', 70, 40, 1);
	}

	&__title {
		text-align: center;
		@media only screen and (max-width: 370px) {
			font-size: 22px;
		}
	}

	&__body {
		max-width: 1188px;
		width: 100%;
		background: $rgbaWhite;
		@include aPx('padding-top', 66, 40, 1);
		@include aPx('padding-bottom', 80, 40, 1);
		@include aPx('padding-left', 150, 15, 1);
		@include aPx('padding-right', 150, 15, 1);
	}

	&__subtitle {
		font-size: 20px;
		line-height: 28px;
		font-weight: 500;
		margin: 0 0 25px;
	}

	&__text,
	&__subtext {
		font-size: 15px;
		line-height: 21px;
		font-weight: 400;
		margin: 0 0 25px;
	}

	&__subitem {
		ul {
			margin: 0 0 25px;
		}
		li {}
		p {
			font-size: 15px;
			line-height: 21px;
			font-weight: 400;
		}
	}
}

