.header {
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	width: 100%;
	z-index: 11;
	transform: translateY(0);
	transition: transform .5s ease-in-out;

	&__top {
		background: $rgbaGrey;
		padding: 10px 0;
		@media (max-width: 620px) {
			display: none;
		}
	}

	&__phones {
		display: flex;
		justify-content: flex-end;
		align-items: center;
	}

	&__phones-item {
		display: flex;
		align-items: center;
	}

	&__body {
		background: $rgbaWhite;
	}

	&__content {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		align-items: center;
		@include aPx('padding-top', 20, 5, 1);
		@include aPx('padding-bottom', 20, 5, 1);
		@media (max-width: 1280px) {
			grid-template-columns: 1fr 1fr;
			grid-template-rows: min-content min-content;
		}
		@media (max-width: 620px) {
			grid-template-columns: 1fr;
		}
	}

	&__content-left {
		display: flex;
		align-items: center;
		@media (max-width: 1024px) {
			flex: 1 1 auto;
			margin: 0 !important;
		}
		@media (max-width: 1280px) {
			grid-column: 1 / 2;
			grid-row: 1 / 2;
			margin: 0 0 15px;
		}
		@media (max-width: 620px) {
			display: grid;
			grid-template-columns: 1fr 1fr 1fr;
			grid-column: unset;
			grid-row: unset;
			margin: unset;
		}
	}

	&__content-right {
		display: flex;
		align-items: center;
		justify-self: flex-end;
		@media (max-width: 1280px) {
			grid-column: 3 / 4;
			grid-row: 1 / 2;
			margin: 0 0 15px;
		}
		@media (max-width: 1024px) {
			margin: 0;
		}
		@media (max-width: 620px) {
			display: none;
		}
	}

	&__login-icon {
		&:before {
			font-size: 28px;
			color: $red;
			transition: all .4s ease 0s;
		}

		margin: 0 26px 0 0;
		@media (any-hover: hover) {
			&:hover:before {
				color: #ff0f00;
				text-shadow: 0 0 20px #ff0f00;
			}
		}
		@media (max-width: 620px) {
			&:before {
				font-size: 24px;

			}
			margin: 0;
		}
	}

	&__burger-icon {
		margin: 0 50px 0 0;
		@media (min-width: 1025px) {
			display: none;
		}
		@media (max-width: 620px) {
			display: flex;
			align-items: center;
			justify-content: flex-start;
		}
	}

	&__logo {
		display: inline-block;
		width: 172px;
		height: 30px;
		cursor: pointer;
		@media (max-width: 620px) {
			grid-column: 2 / 3;
		}
	}

	&__menu {
		@media (max-width: 1360px) {
			@include adaptive(margin-right, 1024px, 1366px, 10px, 20px);
			@include adaptive(margin-left, 1024px, 1366px, 10px, 20px);
		}
		@media (max-width: 1280px) {
			grid-column: 1 / -1;
			grid-row: 2 / 3;
			margin: 0;
		}
	}

	&._hide {
		transform: translateY(-100%);
		transition: transform .4s ease-in-out;
	}
}

.registered {
	.header__content-right {
		@media (max-width: 620px) {
			display: block;
		}

		.header__login-icon {
		}

		.header__log-off {
			@media (max-width: 620px) {
				display: none;
			}
		}

		.header__questionnaire {
			@media (max-width: 620px) {
				display: none;
			}
		}
	}
}

.menu {
	&__body {

		@media (max-width: 1024px) {
			&._active .menu__content {
				display: flex;
			}
		}

		@media (max-width: 620px) {
			&._active .menu__content {
				display: grid;
			}
		}

		&._active {
			animation: open 500ms ease 0s 1 normal forwards;
		}

		.menu__buttons, .menu__footer {
			@media (min-width: 620px) {
				display: none;
			}
		}

		@media (max-width: 1024px) {
			position: absolute;
			left: 0;
			max-width: 340px;
			width: 100%;
			box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.15);
			background: $rgbaWhite;
			animation: close 500ms ease 0s 1 normal forwards;
		}

		@media (max-width: 620px) {
			top: 0;
			max-width: 100%;
			height: 100%;
			height: 100vh;
		}
	}

	&__content {
		width: 100%;
		height: 100%;
		@media (min-width: 1024px) {
			display: flex;
		}

		@media (max-width: 1024px) {
			flex-direction: column;
		}
		@media (max-width: 620px) {
			flex-direction: unset;
			grid-template-rows: min-content 1fr min-content;
			overflow: auto;
		}
	}

	&__header {
		@media (min-width: 1024px) {
			display: none;
		}
	}

	&__close {
		position: absolute;
		top: 23px;
		right: 23px;

		&:before {
			@include aFont(25, 20, 1);
		}
	}

	&__buttons {
		@media (max-width: 620px) {
			padding: 56px 50px 0 50px;
		}
		@media only screen and (max-width: 370px) {
			padding: 56px 10px 0 10px;
		}
	}

	&__list {
		display: flex;
		align-items: center;
		@media (max-width: 1024px) {
			flex-direction: column;
			align-items: flex-start;
			padding: 36px 40px;
		}
		@media (max-width: 620px) {
			padding: 56px 50px;
		}
		@media (max-width: 370px) {
			padding: 56px 10px;
		}
	}

	&__item {
		&:not(:last-child) {
			margin-right: 50px;
			@media (max-width: 1920px) {
				@include adaptive(margin-right, 1366px, 1920px, 26px, 50px);
			}
			@media (max-width: 1360px) {
				@include adaptive(margin-right, 1024px, 1366px, 10px, 26px);
			}
		}

		@media (max-width: 1024px) {
			&:not(:last-child) {
				margin: 0 0 46px;
			}
		}
	}

	&__link {
		position: relative;
		font-size: 13px;
		line-height: 16px;
		font-weight: 400;
		white-space: nowrap;
		transition: font-weight .4s ease 0s;

		&:before {
			position: absolute;
			content: '';
			left: 0;
			bottom: -2px;
			width: 0%;
			height: 1px;
			background: $black;
			z-index: 0;
			transition: width 500ms cubic-bezier(0.000, 0.000, 0.220, 1.055);
			animation-timing-function: cubic-bezier(0.000, 0.000, 0.220, 1.055);
		}

		@media (any-hover: hover) {
			&:hover:before {
				width: 100%;
			}
		}

		&._active {
			font-weight: 500;
		}

		@media (max-width: 1024px) {
			font-size: 20px;
			line-height: 25px;
		}
	}

	&__footer {
		background: $rgbaGrey;
		padding: 30px 50px 25px 50px;
	}

	&__footer-items {
		display: flex;
		align-items: flex-start;

		&:not(:last-child) {
			margin: 0 0 18px;
		}
	}

	&__footer-item {
		display: flex;
		flex-direction: column;
	}
}
