.wrapper._references {
	overflow: unset;
	@media (pointer: coarse) {
		overflow: hidden;
	}
}

.references {

	&__content {
		display: flex;
		flex-direction: column;
		align-items: center;
		@include aPx('padding-top', 100, 70, 1);
		@include aPx('padding-bottom', 100, 70, 1);
	}

	&__header {
		display: flex;
		justify-content: center;
		align-items: center;
		@include aPx('margin-bottom', 70, 40, 1);
	}

	&__title {
		text-align: center;
	}

	&__body {
		max-width: 1188px;
		width: 100%;

		&._scroll {
			.references__filter {
				background: #E5E5E5;
				margin: 0;
				padding: 20px 0;
				z-index: 1;
				@media (pointer: coarse) {
					position: fixed;
					width: 100%;
					@media only screen and (max-width: 768px) {
						position: static;
					}
				}
			}

			.table__header {
				z-index: 1;
				@media (pointer: coarse) {
					position: fixed;
					width: 100%;
					@media only screen and (max-width: 768px) {
						position: static;
					}
				}
			}
		}
	}

	&__filter {
		position: sticky;
		margin: 0 0 37px;
	}

	&__filter-inner {
		@media only screen and (max-width: 767px) {
			border-radius: 10px;
			overflow: hidden;
		}
	}

	&__filter-choice {
		position: relative;
		width: 100%;
		display: flex;
		align-items: center;
		font-size: 15px;
		line-height: 19px;
		font-weight: 500;
		text-transform: uppercase;
		white-space: nowrap;
		background: $rgbaWhite;
		padding: 16px 45px 16px 20px;

		.references__btn-choice-icon {
			position: absolute;
			top: 0;
			bottom: 0;
			right: 20px;
			margin-block: auto;
			width: 20px;
			height: 20px;
			display: flex;
			justify-content: center;
			align-items: center;
			transition: transform .4s ease 0s;

			&:before {
				font-size: 10px;
				color: $red;
			}
		}
		@media only screen and (min-width: 767px) {
			display: none;
		}
		@media only screen and (max-width: 767px) {
			border-radius: unset;
		}

		&._active .references__btn-choice-icon {
			transform: rotate(180deg);
		}
		#choice {
			white-space: nowrap;
			text-overflow: ellipsis;
			overflow: hidden;
		}
	}

	&__list {
		display: none;
		align-items: center;
		gap: 20px;
		@media only screen and (min-width: 767px) {
			display: flex;
		}
		@media only screen and (max-width: 1080px) {
			gap: 10px;
		}
		@media only screen and (max-width: 767px) {
			flex-direction: column;
			align-items: flex-start;
			gap: 0;
		}
	}

	&__item {
		position: relative;

		@media only screen and (max-width: 1080px) {
			flex: 1;
		}
		@media only screen and (max-width: 768px) {
			width: 100%;
		}
	}

	&__btn-icon {
		position: absolute;
		top: -3px;
		right: -3px;
		width: 20px;
		height: 20px;
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 50%;
		background: $bgRed;
		opacity: 0;
		visibility: hidden;
		transition: opacity .4s ease 0s;

		&:before {
			font-size: 13px;
			color: $white;
		}
		@media only screen and (max-width: 767px) {
			top: 0;
			right: 20px;
			bottom: 0;
			margin-block: auto;
		}
	}

	&__btn {
		font-size: 15px;
		line-height: 19px;
		font-weight: 500;
		text-transform: uppercase;
		border: 2px solid $rgbaWhite;
		background: $rgbaWhite;
		padding: 16px 20px;
		transition: all 0.4s ease 0s;

		@media (any-hover: hover) {
			&:hover {
				border-color: $bgRed;
			}
		}

		&._active {
			border-color: $bgRed;

			& ~ .references__btn-icon {
				opacity: 1;
				visibility: visible;
			}
		}

		@media only screen and (max-width: 1080px) {
			width: 100%;
			padding: 15px 0;
		}
		@include adaptive(font-size, 768px, 1080px, 9px, 13px);
		@media only screen and (max-width: 767px) {
			display: flex;
			justify-content: flex-start;
			align-items: center;
			font-size: 15px;
			line-height: 19px;
			font-weight: 500;
			text-transform: uppercase;
			border: none;
			border-radius: unset;
			padding: 16px 20px;
		}
	}


	&__table-overflow {
		@media only screen and (max-width: 767px) {
			overflow-x: auto;
		}
	}
}

.table {
	table-layout: fixed;
	border-spacing: 0;
	border-collapse: collapse;
	background: $rgbaWhite;
	@media only screen and (max-width: 767px) {
		width: 100%;
	}

	&__header {
		position: sticky;
		border: none;
		background: $bgRed;
		@media only screen and (max-width: 768px) {
			position: static;
		}
	}

	&__header-col {
		padding: 10px 0;

		&:not(:last-child) {
			border-right: 1px solid $rgbaWhite;
		}

		&:nth-child(1) {
			width: 430px;
		}

		&:nth-child(2) {
			width: 256px;
		}

		&:nth-child(3) {
			width: 256px;
		}

		&:nth-child(4) {
			width: 123px;
		}

		&:nth-child(5) {
			width: 123px;
		}
	}

	&__header-title {
		font-size: 15px;
		line-height: 140%;
		font-weight: 500;
		text-align: center;
		color: $white;
	}

	&__body {
		display: contents;

		&._hidden {
			display: none;
		}
	}

	&__body-row {
		&--border {
			border-bottom: 1px solid $black;

			.table__body-col {
				border-bottom: 0;
			}
		}

		.table__body-col--no-border {
			border-bottom: 1px solid $borderColor;
		}
	}

	&__body-col {
		text-align: center;
		border-right: 1px solid $borderColor;
		border-bottom: 1px solid $borderColor;
		padding: 15px;

		&--picture {
			vertical-align: top;
		}
	}

	&__flex-box {
		display: flex;
	}

	&__body-col-subtitle {
		text-align: center;
		background: $rgbaGrey;
		padding: 10px;
	}

	&__subtitle {
		font-size: 15px;
		line-height: 140%;
		font-weight: 500;
		text-transform: uppercase;
		color: $white;
	}

	&__picture {
		max-width: 138px;
		width: 100%;
		height: 70px;
		margin: 0 20px 0 0;

		&._imgFit {
			img {
				object-fit: contain;
			}
		}
	}

	&__img {
	}

	&__body-title {
		text-align: left;
	}

	&__body-text {
		font-size: 15px;
		line-height: 19px;
		font-weight: 400;
	}

	&__body-link {
	}
}
