.sidebar {
	max-width: 282px;
	display: flex;
	flex-direction: column;
	align-items: center;

	&__nav {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	&__list {
		width: 100%;
		display: flex;
		flex-direction: column;
	}

	&__sublist {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		display: flex;
		flex-direction: column;
		filter: drop-shadow(0px 4px 10px rgba(0, 0, 0, 0.15));
		background: $rgbaWhite;
		transition: all .4s ease 0s;
		transform: translateX(80%);
		opacity: 0;
		visibility: hidden;
		z-index: 1;
	}

	&__secondlist {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		display: flex;
		flex-direction: column;
		filter: drop-shadow(0px 4px 10px rgba(0, 0, 0, 0.15));
		background: $rgbaWhite;
		transition: all .4s ease 0s;
		transform: translateX(80%);
		opacity: 0;
		visibility: hidden;
		z-index: 1;
	}

	&__item {
		position: relative;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		cursor: pointer;
		padding: 18px 24px;
		transition: background .4s ease 0s;

		&:before {
			position: absolute;
			content: '';
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			width: 106%;
			height: 100%;
		}

		&._active {
			background: $rgbaWhite;

			.sidebar__icon-arrow {
				display: none;
				@media only screen and (max-width: 767px) {
					display: flex;
				}
			}
		}

		@media (any-hover: hover) {
			&:hover {
				background: $rgbaWhite;

				& > .sidebar__sublist {
					transform: translateX(106%);
					opacity: 1;
					visibility: visible;
				}
			}
		}
	}

	&__subitem {
		position: relative;
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		cursor: pointer;
		padding: 18px 24px;
		transition: background .4s ease 0s;

		&:before {
			position: absolute;
			content: '';
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			width: 106%;
			height: 100%;
		}

		&._active {
			background: #E5E5E5;
		}

		@media (any-hover: hover) {
			&:hover {
				background: $rgbaBgModal;

				& > .sidebar__secondlist {
					transform: translateX(106%);
					opacity: 1;
					visibility: visible;
				}
			}
		}

		.sidebar__icon {
			width: 0;
			margin: 0;
		}
	}

	&__seconditem {
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		cursor: pointer;
		padding: 18px 24px;
		transition: background .4s ease 0s;
		@media (any-hover: hover) {
			&:hover {
				background: $rgbaBgModal;
			}
		}
		&._active {
			background: $rgbaBgModal;
		}
	}

	&__icon {
		width: 35px;
		height: 35px;
		display: flex;
		justify-content: center;
		align-items: center;
		font-style: normal;
		margin: 0 18px 0 0;

		&:before {
			font-size: 30px;
			color: $red;
		}
	}

	&__link {
		position: relative;
		width: 170px;
		display: inline-block;
		flex: 1 1 auto;
		font-size: 13px;
		line-height: 15px;
		font-weight: 500;
		margin: 0 10px 0 0;
		z-index: 1;
	}

	&__sublink {
		position: relative;
		flex: 1 1 auto;
		font-size: 13px;
		line-height: 16px;
		font-weight: 500;
		z-index: 1;
	}

	&__secondlink {
		position: relative;
		flex: 1 1 auto;
		font-size: 13px;
		line-height: 16px;
		font-weight: 500;
		z-index: 1;
	}

	&__arrow {
		display: inline-block;
		font-style: normal;

		&:before {
			font-size: 10px;
			color: $red;
		}
	}

	&__btn {
	}

	&__icon-arrow {
		display: none;
		position: absolute;
		top: 18px;
		right: 24px;
		width: 50px;
		height: 35px;
		justify-content: flex-end;
		align-items: center;
		z-index: 5;

		&:before {
			color: $red;
		}

		@media only screen and (min-width: 767px) {
			display: none;
		}
	}
}
