.about {

	&__content {
		display: flex;
		flex-direction: column;
		align-items: center;
		@include aPx('padding-top', 100, 70, 1);
		@include aPx('padding-bottom', 100, 70, 1);
	}

	&__header {
		display: flex;
		justify-content: center;
		align-items: center;
		@include aPx('margin-bottom', 60, 40, 1);
	}

	&__subtitle {
		font-size: 15px;
		line-height: 140%;
		font-weight: 400;
		@media only screen and (max-width: 768px) {
			text-align: center;
		}
	}

	&__inner {
		max-width: 1188px;
		width: 100%;
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
		justify-content: space-between;
		gap: 20px;
		@include aPx('margin-bottom', 100, 60, 1);
		@media only screen and (max-width: 1280px) {
			grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
		}
		@media only screen and (max-width: 1180px) {
			grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
		}
		@media only screen and (max-width: 992px) {
			grid-template-columns: repeat(auto-fit, minmax(33.33333%, 1fr));
		}
		@media only screen and (max-width: 530px) {
			grid-template-columns: repeat(auto-fit, minmax(100%, 1fr));
		}
	}

	&__card {
		display: flex;
		flex-direction: column;
		align-items: center;
		text-align: center;
		background: $rgbaWhite;
		padding: 15px;
		@media (any-hover: hover) {
			&:hover {
				.about__img {
					filter: unset;
				}
			}
		}
		@media only screen and (max-width: 530px) {
			flex-direction: row;
		}
	}

	&__picture {
		@include aPx('width', 160, 120, 1);
		@include aPx('height', 160, 120, 1);
		display: flex;
		justify-content: center;
		align-items: center;
		&._imgFit .about__img {
			object-fit: contain;
		}
		@media only screen and (max-width: 530px) {
			width: 30%;
			margin: 0 20px 0 0;
		}
	}

	&__card-info {
		height: 40%;
		display: grid;
		grid-template-columns: 1fr;
		grid-template-rows: repeat(2, 1fr);
		@media only screen and (max-width: 530px) {
			width: 70%;
			height: unset;
			text-align: left;
		}
	}

	&__img {
		filter: grayscale(1);
		transition: all .4s ease 0s;
		@media only screen and (max-width: 530px) {
			filter: unset;
		}
	}

	&__card-title {
		font-size: 20px;
		line-height: 140%;
		font-weight: 500;
		@media only screen and (max-width: 320px) {
			font-size: 14px;
		}
	}

	&__card-text {
		font-size: 15px;
		line-height: 140%;
		font-weight: 400;
		@media only screen and (max-width: 320px) {
			font-size: 14px;
			line-height: 1;
		}
	}

	&__body {
		max-width: 1188px;
		width: 100%;
		display: flex;
		gap: 20px;
		@media only screen and (max-width: 620px) {
			flex-direction: column;
		}
		@media only screen and (max-width: 530px) {
			gap: unset;
		}
	}

	&__left {
		flex: 1;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		.about__text {
			&:nth-child(1) {
				margin: 0 0 25px;
			}
			&:nth-child(2) {
				@include aPx('margin-bottom', 100, 40, 1);
				@media only screen and (max-width: 530px) {
					margin: 0 0 25px;
				}
			}
		}
		.about__image {
			@media only screen and (max-width: 530px) {
				margin: 0 0 70px;
			}
		}
	}

	&__text {
		width: 72%;
		font-size: 15px;
		line-height: 140%;
		font-weight: 400;
		@media only screen and (max-width: 992px) {
			width: 80%;
		}
		@media only screen and (max-width: 768px) {
			width: 90%;
		}
		@media only screen and (max-width: 530px) {
			width: 100%;
		}
	}

	&__right {
		flex: 1;
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		.about__image {
			@include aPx('margin-bottom', 70, 60, 1);
			@media only screen and (max-width: 530px) {
				order: 2;
				margin: 0;
			}
		}
		.about__text {
			&:nth-child(2) {
				margin: 0 0 25px;
			}
			&:nth-child(3) {
				font-weight: 500;
				margin: 0 0 25px;
			}

			&:nth-child(4) {
				font-weight: 500;
				@media only screen and (max-width: 530px) {
					margin: 0 0 25px;
				}
			}
		}
	}
}
