.reviews-page {

	&__content {
		display: flex;
		flex-direction: column;
		align-items: center;
		@include aPx('padding-top', 100, 70, 1);
		@include aPx('padding-bottom', 100, 70, 1);
	}

	&__header {
		display: flex;
		justify-content: center;
		align-items: center;
		@include aPx('margin-bottom', 70, 40, 1);
	}

	&__body {
		max-width: 1188px;
		width: 100%;
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(282px, 1fr));
		gap: 20px;
		margin: 0 0 70px;
		@media only screen and (max-width: 1280px) {
			grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
		}
		@media only screen and (max-width: 1140px) {
			grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
		}
	}

	&__card {
		position: relative;
		@include adaptive(height, 768px, 1920px, 180px, 190px);
		border: 1px solid $rgbaWhite;
		background: $rgbaWhite;
		padding: 16px 16px 20px 16px;
		transition: all .4s ease 0s;
		@media (any-hover: hover) {
			&:hover {
				border-color: $black;
				transform: translateY(-5px);
			}
		}

		&._hidden {
			@media only screen and (max-width: 768px) {
				display: none;
			}
		}
	}

	&__link {
		height: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: space-between;
		@media only screen and (max-width: 1024px) {
			justify-content: space-evenly;
		}
	}

	&__picture {
		@include adaptive(width, 768px, 1920px, 100px, 216px);
		@include adaptive(height, 768px, 1920px, 50px, 110px);
		display: flex;
		justify-content: center;
		align-items: center;
	}

	&__img {
		object-fit: contain !important;
	}

	&__note {
		font-size: 15px;
		line-height: 120%;
		font-weight: 500;
		text-align: center;
		color: $black-a;
	}

	&__icon {
		position: absolute;
		top: 16px;
		right: 16px;

		&:before {
			color: $red;
		}
	}

	&__form {
		max-width: 1188px;
		width: 100%;

		.form__item {
			&:not(:last-child) {
				margin: 0 0 15px;
			}
		}
	}

	&__subtittle {
		margin: 0 0 54px;
	}

	&__btn-mobile-show {
		position: relative;
		font-size: 15px;
		line-height: 19px;
		font-weight: 500;
		text-align: center;
		margin: 0 0 70px;

		&:before {
			position: absolute;
			content: '';
			left: 0;
			bottom: -2px;
			width: 100%;
			height: 1px;
			background: $black;
			z-index: 0;
			transition: opacity .4s ease 0s;
		}

		@media (any-hover: hover) {
			&:hover:before {
				opacity: 0;
				visibility: hidden;
			}
		}

		@media only screen and (min-width: 768px) {
			display: none;
		}
	}
}

.form {

	&__body {
		width: 100%;
		display: flex;
		margin: 0 0 40px;
		@media only screen and (max-width: 767px) {
			flex-direction: column;
			align-items: center;
		}
	}

	&__left {
		width: 100%;
		flex: 1;
		@include aPx('margin-right', 34, 0, 1);
		@media only screen and (max-width: 767px) {
			margin: 0 0 15px;
		}
	}

	&__right {
		width: 100%;
		flex: 1;
	}

	&__textarea {
		width: 100%;
		height: 100%;
		resize: none;
		padding: 15px 12px;
		@media only screen and (max-width: 767px) {
			height: 275px;
		}
	}

	&__footer {
		align-self: flex-end;
		@media only screen and (max-width: 767px) {
			width: 100%;
		}
	}

	&__btn {
		@media only screen and (max-width: 767px) {
			width: 100%;
		}
	}

	input::placeholder,
	textarea::placeholder {
		font-size: 15px;
		line-height: 19px;
		font-weight: 400;
		color: #A4A4A4;
	}
}

._reviews-page .reviews-page {

	&__content {
		@include aPx('padding-top', 100, 28, 1);
		@include aPx('padding-bottom', 100, 70, 1);
	}

	&__body {
		display: flex;
		justify-content: space-between;
		grid-template-columns: unset;
		gap: 20px;
		@include aPx('margin-bottom', 70, 40, 1);
		@media only screen and (max-width: 767px) {
			flex-direction: column;
			gap: 0;
		}
	}

	span {
		display: inline-block;
	}

	.preview-reviews {
		&__btn {
			display: inline-block;
			margin: 0 0 54px;

			&:before {
				font-weight: 700;
				color: $rgbaGrey-three;
				transition: all .4s ease 0s;
			}
			@media (any-hover: hover) {
				&:hover:before {
					color: rgba(0, 0, 0, 1);
				}
			}

			@media only screen and (max-width: 767px) {
				margin: 0 0 27px;
			}
		}

		&__items {
			@media only screen and (max-width: 767px) {
				display: none;
			}
		}

		&__item {
			display: flex;
			flex-direction: column;
			align-items: center;
		}

		&__picture {
			@include aPx('width', 130, 130, 1);
			@include aPx('height', 190, 190, 1);
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 0 0 18px;
		}

		&__img {
			@include aPx('width', 93, 90, 1);
			@include aPx('height', 160, 160, 1);
		}

		&__icon {
			&:before {
				font-size: 28px;
				font-weight: 700;
				color: $rgbaGrey-three;
				transition: all .4s ease 0s;
			}
			@media (any-hover: hover) {
				&:hover:before {
					color: rgba(0, 0, 0, 1);
				}
			}
		}
	}

	&__inner {
		max-width: 971px;
		width: 100%;
		background: $rgbaWhite;
		@include aPx('padding-top', 64, 36, 1);
		@include aPx('padding-right', 85, 10, 1);
		@include aPx('padding-bottom', 64, 36, 1);
		@include aPx('padding-left', 85, 10, 1);
	}

	.inner-reviews {
		&__title {
			font-size: 20px;
			line-height: 140%;
			font-weight: 500;
			text-align: center;
			color: $black-a;
			margin: 0 0 46px;
			@media only screen and (max-width: 767px) {
				font-size: 18px;
			}
		}

		&__body {
			display: flex;
			flex-direction: column;
		}

		&__text {
			&:not(:last-child) {
				margin: 0 0 25px;
			}

			&:nth-child(6) {
				margin: 0 0 50px;
			}
		}

		&__text, .inner-reviews__subtitle {
			font-size: 15px;
			line-height: 140%;
			font-weight: 400;
		}

		&__subtitle {
		}

		&__list {
			margin: 0 0 25px;
		}

		&__item {
			&:not(:last-child) {
				margin: 0 0 25px;
			}
		}

		&__note {
			margin: 0 0 25px;
		}

		&__note, .inner-reviews__date {
			font-size: 15px;
			line-height: 21px;
			font-weight: 500;
		}
	}

	&__mobile-block {
		align-self: flex-start;
		.preview-reviews__items {
			@media only screen and (max-width: 767px) {
				display: block;
			}
		}
		@media only screen and (min-width: 767px) {
			display: none;
		}
		margin: 0 0 70px;
	}
}
