.info {
	background: $rgbaWhite;
	&__content {
		display: flex;
		flex-direction: column;
		align-items: center;
		@include aPx('padding-top', 100, 70, 1);
		@include aPx('padding-bottom', 100, 70, 1);
	}
	&__header {
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 0 0 60px;
	}
	&__title {
		text-align: center;
	}
	&__body {
		max-width: 1188px;
		width: 100%;
		display: grid;
		grid-template-columns: repeat(3, 280px);
		justify-content: space-between;
		row-gap: 50px;
		margin: 0 0 62px;
		@media only screen and (max-width: 992px) {
			grid-template-columns: repeat(3, 250px);
		}
		@media only screen and (max-width: 768px) {
			grid-template-columns: repeat(3, 1fr);
			column-gap: 10px;
		}
		@media only screen and (max-width: 620px) {
			grid-template-columns: repeat(1, 280px);
			justify-content: center;
		}
	}
	&__card {
		display: flex;
		flex-direction: column;
		align-items: center;
		@media (any-hover: hover) {
			&:hover {
				.info__icon:before {
					color: #ff0f00;
					text-shadow: 0 0 20px #ff0f00;
				}
			}
		}
	}
	&__icon {
		margin: 0 0 20px;
		&:before {
			font-size: 34px;
			color: $red;
			transition: all .4s ease 0s;
		}
	}
	&__text {
		font-size: 15px;
		line-height: 19px;
		font-weight: 400;
		text-align: center;
		color: $black;
	}
	&__footer {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	&__note {
		font-size: 15px;
		line-height: 19px;
		font-weight: 400;
		color: #131D24;
		margin: 0 0 27px;
	}
	&__btn {
	}
}
