.modal.modal-register {
	&:before {
		background: $rgbaBgModal;
	}
	.modal__body {
		@media only screen and (max-width: 420px) {
			padding-left: 15px;
			padding-right: 15px;
		}
	}
	.modal__content {
		@media only screen and (max-width: 420px) {
			padding-top: 10px;
			padding-bottom: 50px;
		}
	}
	.modal__header {
		@media only screen and (max-width: 420px) {
			margin: 0 0 10px;
		}
	}

	.modal__body {
		flex-direction: column;
		align-items: center;
		border-radius: 20px;
		background: $rgbaWhite;
		@include aPx('padding-top', 57, 33, 1);
		@include aPx('padding-right', 34, 30, 1);
		@include aPx('padding-bottom', 77, 38, 1);
		@include aPx('padding-left', 34, 30, 1);
	}
}

.modal {

	&__body {

	}

	&__register-title {
		@media only screen and (max-width: 420px) {
			line-height: 90%;
			text-align: center;
			margin: 0 0 10px;
		}
	}

	&__register-note {
		font-size: 15px;
		line-height: 19px;
		font-weight: 400;
		text-align: center;
		margin: 0 0 37px;
	}

	&__register-form {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	&__register-inner {
		display: flex;
		margin: 0 0 40px;
		@media only screen and (max-width: 620px) {
			flex-direction: column;
		}
	}

	&__register-left {
		max-width: 350px;
		width: 100%;
		margin: 0 34px 0 0;

		.form__item {
			&:not(:last-child) {
				margin: 0 0 15px;
			}
		}

		@media only screen and (max-width: 768px) {
			margin: 0 15px 0 0;
		}

		@media only screen and (max-width: 620px) {
			max-width: 100%;
			margin: 0 0 30px;
		}
	}

	&__register-subtitle {
		font-size: 15px;
		line-height: 19px;
		font-weight: 500;
		margin: 0 0 20px;
	}

	&__register-right {
		max-width: 486px;
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}

	&__register-items {
		display: flex;
		flex-wrap: wrap;
		gap: 14px;

		.form__item {
			position: relative;
			width: auto;
			margin: 0;
			@media only screen and (max-width: 420px) {
				&:last-child {
					grid-column: 1 / 3;
				}
			}
		}

		input[type=checkbox] {
			position: absolute;
			width: 0;
			height: 0;
			opacity: 0;
			visibility: hidden;
			transition: all 0.4s ease 0s;

			&:checked + .form__label{
				border-color: $bgRed;
			}
		}

		.form__label {
			position: relative;
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			border: 2px solid rgba(0, 0, 0, .1);
			border-radius: 10px;
			padding: 0 20px;
			transition: all 0.4s ease 0s;

			@media (any-hover: hover) {
				&:hover {
					border-color: #B3B3B3;
				}
			}
		}

		@include aPx('margin-bottom', 40, 25, 1);
		@media only screen and (max-width: 420px) {
			display: grid;
			grid-template-columns: 1fr 1fr;
			grid-template-rows: repeat(3, 1fr);
		}
	}

	&__register-text {
		font-size: 15px;
		line-height: 19px;
		font-weight: 400;
		margin: 0 0 15px;
	}

	&__register-radio-items {
		&:not(:last-child) {
			@include aPx('margin-bottom', 34, 25, 1);
		}
	}

	&__register-radio-item {
		display: flex;
		align-items: center;
		&:not(:last-child) {
			margin: 0 0 10px;
		}
	}

	&__register-radio-btn {

	}

	&__register-radio-text {
		font-size: 15px;
		line-height: 19px;
		font-weight: 400;
	}

	&__register-footer {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	&__register-btn {
		margin: 0 0 15px;
	}

	&__register-or {
		line-height: 19px;
		font-size: 15px;
		font-weight: 400;
		text-align: center;
	}

	&__register-link {
		position: relative;
		font-size: 15px;
		line-height: 19px;
		font-weight: 500;
		text-align: center;
		transition: color .4s ease 0s;
		z-index: 1;

		&:before {
			position: absolute;
			content: '';
			left: 0;
			bottom: -1px;
			width: 100%;
			height: 1px;
			background: $black;
			transition: background .4s ease 0s;
		}
		@media (any-hover: hover) {
			&:hover {
				color: rgba(0, 0, 0, 0.5);
				&:before {
					background: rgba(0, 0, 0, 0.5);
				}
			}
		}
	}
}

.form {

	&__input-check-text {
		font-size: 15px;
		line-height: 19px;
		font-weight: 400;
		cursor: pointer;
	}

	&__radio {
		display: none;
		&:checked + .form__radio-label::after {
			transform: scale(1);
		}
	}

	&__radio-label {
		position: relative;
		display: inline-flex;
		align-items: center;
		font-size: 16px;
		line-height: 140%;
		cursor: pointer;
		&::before {
			content: '';
			align-self: center;
			flex: 0 0 20px;
			height: 20px;
			background-color: #fff;
			border-radius: 50%;
			border: 2px solid #D8D8D8;
			margin: 0 23px 0 0;
		}
		&::after {
			position: absolute;
			content: '';
			top: 12px;
			left: 3px;
			width: 14px;
			height: 14px;
			border-radius: 50%;
			background: $bgRed;
			transform: scale(0);
			transition: transform 0.5s ease 0s;
		}
	}
}
