.references-modal {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	&__slider {
		&_viewport {
			overflow: hidden;
			.modal__slider-wrapper {
				//width: 526px;
				//height: 788px;
				@include aPx('width', 526, 290, 1);
				@include aPx('height', 788, 450, 1);
				.modal__slider-slide {
					display: flex;
					flex-direction: column;
					align-items: center;
				}
				.modal__slider-picture {
					width: 100%;
					height: 100%;
					margin: 0 0 20px;
				}
				@media only screen and (max-width: 620px) {

				}
			}
			margin: 0 0 40px;
		}
		&_thumbs {
			overflow: hidden;
			.modal__slider-wrapper {
				//width: 290px;
				//height: 90px;
				@include aPx('width', 290, 200, 1);
				@include aPx('height', 90, 60, 1);
				.modal__slider-slide {
					//width: 90px;
					//height: 90px;
					@include aPx('width', 90, 60, 1);
					@include aPx('height', 90, 60, 1);
					display: flex;
					justify-content: center;
					align-items: center;
					border: 1px solid $rgbaGrey-three;

					&.swiper-slide-active {
						border-color: $black;
					}
				}
				.modal__slider-picture {
					//width: 74px;
					//height: 74px;
					@include aPx('width', 74, 50, 1);
					@include aPx('height', 74, 50, 1);
				}
			}
		}
	}

	&__text {
		@media only screen and (max-width: 620px) {
			text-align: center;
		}
	}

	.modal__info {
		position: absolute;
		bottom: 0;
		max-width: 400px;
		//height: 90px;
		@include aPx('height', 90, 68, 1);
	}

	.modal__control {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		padding: 0;
		margin-inline: auto;
		margin-block: auto;
	}

}
