.product {

	&__content {
		display: flex;
		flex-direction: column;
		align-items: center;
		@include aPx('padding-top', 40, 40, 1);
		@include aPx('padding-bottom', 100, 70, 1);
	}

	&__nav {
		position: relative;
		max-width: 318px;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		align-self: flex-start;
		@include aPx('margin-bottom', 47, 40, 1);
		@media only screen and (max-width: 767px) {
			max-width: 100%;
		}
	}

	&__nav-hover-menu {
		&:before {
			position: absolute;
			content: '';
			top: 100%;
			left: 67%;
			width: 34%;
			height: 100%;
			padding: 0 0 80px 0;
		}

		@media (any-hover: hover) {
			&:hover {
				.product__catalog-menu {
					transform: translateY(10%);
					opacity: 1;
					visibility: visible;
				}

				.product__burger:before {
					color: rgba(0, 0, 0, 1);
				}
			}
		}

		&._hover {
			.product__catalog-menu {
				transform: translateY(10%);
				opacity: 1;
				visibility: visible;
			}

			.product__burger:before {
				color: rgba(0, 0, 0, 1);
			}
		}
	}

	&__back-link {
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 15px;
		line-height: 19px;
		font-weight: 400;

		&:before {
			color: $red;
			margin: 0 20px 0 0;
			transition: color .4s ease 0s;
		}

		@media (any-hover: hover) {
			&:hover:before {
				color: rgba(0, 0, 0, 1);
			}
		}
	}

	&__burger {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		font-weight: 400;
		font-size: 15px;
		line-height: 19px;
		cursor: pointer;
		z-index: 1;

		&:before {
			color: $red;
			margin: 0 20px 0 0;
			transition: color .4s ease 0s;
		}

		@media (any-hover: hover) {
			&:hover:before {
				color: rgba(0, 0, 0, 1);
			}
		}
	}

	&__catalog-menu {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
		background: $rgbaBgModal;
		padding: 0 0 30px;
		transition: transform .4s ease 0s;
		transform: translateY(30%);
		opacity: 0;
		visibility: hidden;
		z-index: 2;

		.sidebar__list {
			margin: 0 0 40px;

			.sidebar__arrow {
				position: relative;
				z-index: 1;
			}

		}
	}

	&__main {
		max-width: 1188px;
		width: 100%;

		& > h3.product__title._subtitle {
			margin: 0 0 40px;
		}
	}

	&__header {
		display: flex;
		border-bottom: 1px solid $bgRed;
		@include aPx('padding-bottom', 80, 70, 1);
		@include aPx('margin-bottom', 60, 40, 1);
		@media only screen and (max-width: 767px) {
			flex-direction: column;
		}
	}

	&__header-left {
		display: flex;
		gap: 20px;
		@include aPx('margin-right', 62, 0, 1);
		@media only screen and (max-width: 767px) {
			flex-direction: column;
		}
		@media only screen and (max-width: 767px) {
			margin: 0 0 60px;
		}
	}

	&__slider {
		overflow: hidden;

		&_viewport {
			//width: 433px;
			//height: 433px;
			@include adaptive(width, 768px, 1024px, 250px, 433px);
			@include adaptive(height, 768px, 1024px, 250px, 433px);

			.modal__slider-picture {
				width: 100%;
				height: 100%;
			}

			.modal__slider-img {
				object-fit: contain !important;
			}

			@media only screen and (max-width: 767px) {
				width: 100%;
			}
			@include adaptive(height, 320px, 767px, 300px, 375px);
		}

		&_thumbs {
			//height: 290px;
			@include adaptive(height, 768px, 1024px, 170px, 290px);
			@media only screen and (max-width: 767px) {
				width: 260px;
				height: 100%;
			}

			.modal__slider-wrapper {
				display: flex;
				flex-direction: column;
				@media only screen and (max-width: 767px) {
					flex-direction: row;
				}
			}

			.modal__slider-slide {
				//width: 90px;
				//height: 90px;
				@include adaptive(width, 768px, 1024px, 50px, 90px);
				@include adaptive(height, 768px, 1024px, 50px, 90px);
				display: flex;
				justify-content: center;
				align-items: center;

				&.swiper-slide-thumb-active {
					border-color: $black;
				}
				@media only screen and (max-width: 767px) {
					width: 80px;
					height: 80px;
				}
			}

			.modal__slider-picture {
				width: 74px;
				height: 74px;
				@include adaptive(width, 768px, 1024px, 40px, 74px);
				@include adaptive(height, 768px, 1024px, 40px, 74px);
				@media only screen and (max-width: 767px) {
					width: 65px;
					height: 65px;
				}
			}
		}
	}

	&__header-right {
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		@media only screen and (max-width: 1220px) {
			align-items: flex-start;
		}
		@media only screen and (max-width: 767px) {
			align-items: center;
		}
	}

	&__title {
		flex: 0 0 30%;
		font-size: 30px;
		line-height: 38px;
		font-weight: 500;
	}

	&__title_mobile {
		font-size: 25px;
		margin: 0 0 30px;
		@media only screen and (min-width: 767px) {
			display: none;
		}
	}

	&__title_desctop {
		@media only screen and (max-width: 767px) {
			display: none;
		}
	}

	&__buttons {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex: 1 1 80%;
		gap: 10px;
		@media only screen and (max-width: 1220px) {
			flex-direction: column;
			align-items: flex-start;
			margin: 0 0 67px;
		}
		@media only screen and (max-width: 767px) {
			align-items: center;
			margin: 0 0 42px;
		}
	}

	&__bth {

		&_request {
		}

		&_consultation {
			border-color: $black;
			color: $black;
			background: inherit;
			transition: border-color .4s ease 0s;
			@media (any-hover: hover) {
				&:hover {
					border-color: $bgRed;
					background: inherit;
				}
			}
		}
	}

	&__items-link {
		display: flex;
		align-items: center;
		justify-content: space-between;
		@media only screen and (max-width: 1220px) {
			flex-direction: column;
		}
		.product__items-item:nth-child(2) {
			.product__items-picture {
				display: unset;
				.product__items-icon {
					position: absolute;
					top: 0;
					left: 0;
					right: 0;
					bottom: 0;
					margin-inline: auto;
					margin-block: auto;
				}
			}
		}
	}

	&__items-item {
		width: 280px;
		display: flex;
		align-items: center;
		@media only screen and (max-width: 1220px) {
			&:not(:last-child) {
				margin: 0 0 25px;
			}
		}
		@media only screen and (max-width: 767px) {
			justify-content: center;
			&:not(:last-child) {
				margin: 0 0 18px;
			}
		}
	}

	&__items-picture {
		position: relative;
		width: 56px;
		height: 56px;
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 0 18px 0 0;
	}

	&__items-img {
	}

	&__items-item-link {
		position: relative;
		display: inline-block;
		font-size: 15px;
		line-height: 19px;
		font-weight: 500;
		transition: color .4s ease 0s;

		&:before {
			position: absolute;
			content: '';
			left: 0;
			bottom: -1px;
			width: 100%;
			height: 1px;
			background: $black;
			z-index: 0;
			transition: background .4s ease 0s;
		}

		@media (any-hover: hover) {
			&:hover {
				color: rgba(0, 0, 0, 0.5);

				&:before {
					background: rgba(0, 0, 0, 0.5);
				}
			}
		}
	}

	&__items-icon {
		width: 48px;
		height: 48px;
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 50%;
		border: 1px solid $bgRed;
		margin: 0 18px 0 0;

		&:before {
			font-size: 24px;
			color: $red;
		}
	}

	&__body {
		display: flex;
		flex-direction: column;
		border-bottom: 1px solid $bgRed;
		@include aPx('padding-bottom', 80, 70, 1);
		@include aPx('margin-bottom', 60, 40, 1);

		& > .product__body-subtitle {
			margin: 0 0 15px;
		}
	}

	&__body-title {
		margin: 0 0 40px;
	}

	&__inner {
		display: flex;
		flex-direction: column;
		gap: 20px;
		margin: 0 0 60px;
	}

	&__inner-top {
		display: grid;
		grid-template-columns: 1fr 1fr;
		gap: 20px;
		margin: 0 0 40px;
		@media only screen and (max-width: 767px) {
			grid-template-columns: 1fr;
		}
	}

	&__inner-top-left {
		display: flex;
		flex-direction: column;
		gap: 25px;
	}

	&__inner-body {
		display: flex;
		gap: 20px;
		@media only screen and (max-width: 767px) {
			flex-direction: column;
		}
	}

	&__left {
		flex: 1;
	}

	&__text {
		font-size: 15px;
		line-height: 140%;
		font-weight: 400;
	}

	&__left-item {
		display: flex;
		flex-direction: column;

		&:not(:last-child) {
			margin: 0 0 25px;
		}
	}

	&__body-subtitle {
		display: inline-block;
		font-size: 15px;
		line-height: 140%;
		font-weight: 500;
		text-transform: uppercase;
	}

	&__note {
		font-size: 15px;
		line-height: 28px;
		font-weight: 400;
	}

	&__right {
		flex: 1;

		.product__text {
			&:nth-child(1) {
				margin: 0 0 25px;
			}
		}
	}

	&__table {
		width: 100%;
		margin: 0 0 10px;

		&_one {
		}
	}

	&__body-note {
		font-weight: 400;
		font-size: 12px;
		line-height: 140%;
	}

	&__tabs {
	}

	&__filter {
		margin: 0 0 60px;

		.references__item._active .references__btn {
			border-color: $bgRed;

			& ~ .references__btn-icon {
				opacity: 1;
				visibility: visible;
			}
		}
	}

	&__filter-inner {
	}

	&__tab {
		display: none;

		&._active {
			display: block;
		}
	}


	&__tab-title {
		display: inline-block;
		font-size: 15px;
		line-height: 140%;
		font-weight: 500;
		text-transform: uppercase;
		margin: 0 0 15px;
	}

	&__tab-picure {
		width: 585px;
		height: 486px;
		background: $rgbaWhite;
		margin: 0 0 40px;
		@include adaptive(height, 320px, 767px, 300px, 486px);
		@media only screen and (max-width: 767px) {
			width: 100%;
		}
	}

	&__tab-img {
		object-fit: contain !important;
	}

	&__tab-table {
		width: 100%;
	}

	&__tab-overflow {
		@media only screen and (max-width: 767px) {
			width: 100%;
			overflow-x: auto;
		}
	}
}

.table {

	&__product {
	}

	&__product-header {
		th.table__product-col {
			background: $bgRed;
			padding: 10px 5px;
		}
	}

	&__product-row {
	}

	&__product-col {
		text-align: center;
		background: $rgbaWhite;
		padding: 15px 5px;

		b.table__product-text {
			font-weight: 500;
		}
	}

	&__product-title {
		font-size: 15px;
		line-height: 140%;
		font-weight: 500;
		color: $white;
	}

	&__product-body {
	}

	&__product-text {
		font-size: 15px;
		line-height: 19px;
		font-weight: 400;
	}

	&__tab {
		width: 100%;
	}

	&__tab-header {
		th.table__tab-col {
			background: $bgRed;
			padding: 10px 26px;
		}
	}

	&__tab-row {
	}

	&__tab-col {
		text-align: center;
		background: $rgbaWhite;
		padding: 15px 26px;
	}

	&__tab-title {
		font-size: 15px;
		line-height: 140%;
		font-weight: 500;
		color: $white;
	}

	&__tab-body {
		.table__tab-row {
			&:nth-child(1) {
				font-weight: 400;
			}
		}
	}

	&__tab-text {
		white-space: nowrap;
	}
}
