.catalog {

	&__content {
		display: flex;
		flex-direction: column;
		align-items: center;
		@include aPx('padding-top', 100, 60, 1);
		@include aPx('padding-bottom', 100, 60, 1);
	}

	&__header {
		max-width: 1188px;
		width: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;
		@include aPx('margin-bottom', 46, 30, 1);
		@media only screen and (max-width: 768px) {
			flex-direction: column;
		}
	}

	&__title {
		@media only screen and (max-width: 768px) {
			align-self: flex-start;
			margin: 0 0 40px;
		}
	}

	&__search {
		max-width: 886px;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: flex-end;
		@media only screen and (max-width: 768px) {
			max-width: 100%;
		}
	}

	&__search-item {
		position: relative;
		width: 100%;
		overflow: hidden;
		background: $rgbaWhite;
		margin: 0 36px 0 0;
		@media only screen and (max-width: 1024px) {
			max-width: 490px;
		}
		@media only screen and (max-width: 768px) {
			max-width: 100%;
		}
		@media only screen and (max-width: 767px) {
			margin: 0;
		}
	}

	&__label {
		position: absolute;
		top: 0;
		bottom: 0;
		right: 18px;
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		z-index: 1;

		&:before {
			color: rgba(0, 0, 0, 0.3);
		}
	}

	&__input {
		width: 100%;
		border: none;
		@include aPx('padding-top', 16, 15, 1);
		@include aPx('padding-right', 18, 10, 1);
		@include aPx('padding-bottom', 16, 15, 1);
		@include aPx('padding-left', 18, 10, 1);

		&::placeholder {
			font-size: 15px;
			line-height: 19px;
			font-weight: 400;
			color: rgba(0, 0, 0, 0.3);
		}

		&:not(:placeholder-shown) {
			& ~ .catalog__label:before {
				display: none;
			}
		}

		&:focus {
			& ~ .catalog__label:before {
				transition: color .4s ease 0s;
				color: rgba(0, 0, 0, 1);
			}
		}
	}

	&__btn {
		@media only screen and (max-width: 767px) {
			display: none;
		}
	}

	&__body {
		display: flex;
		gap: 20px;
		@media only screen and (max-width: 767px) {
			flex-direction: column;
			gap: 40px;
		}
	}

	&__left {
		.sidebar__list {
			margin: 0 0 40px;
		}

		.sidebar__btn {
			@media only screen and (max-width: 767px) {
				margin-inline: auto;
			}
		}
	}

	&__right {
		max-width: 886px;
		width: 100%;

		._tabs-block {
			display: none;

			&._active {
				display: flex;
			}
		}

		.catalog__card-text.catalog__card-text--top.catalog__card-text--show {
			@media only screen and (max-width: 767px) {
				margin: 0 0 -58px 0;

				&._more {
					margin: 0 0 38px 0;

					&:before {
						background: linear-gradient(180deg, rgba($rgbaBgModal, 0) 100%, $rgbaBgModal 100%);
					}
				}
			}
		}
	}

	&__right-inner {
		display: flex;
		flex-direction: column;
	}

	&__subtitle {
		margin: 0 0 40px;
	}

	&__right-body {
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(433px, 1fr));
		gap: 20px;
		@media only screen and (max-width: 1280px) {
			grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
			gap: 10px;
		}
		@media only screen and (max-width: 1180px) {
			grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
		}
		@media only screen and (max-width: 1080px) {
			grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
		}
		@media only screen and (max-width: 370px) {
			grid-template-columns: repeat(auto-fit, minmax(100%, 1fr));
		}
	}

	&__card {
		height: 380px;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: space-between;
		border: 1px solid $rgbaWhite;
		background: $rgbaWhite;
		@include aPx('padding-top', 26, 20, 1);
		@include aPx('padding-right', 40, 10, 1);
		@include aPx('padding-bottom', 40, 30, 1);
		@include aPx('padding-left', 40, 10, 1);
		transition: all .4s ease 0s;
		@media (any-hover: hover) {
			&:hover {
				border-color: $black;
				transform: translateY(-4px);
			}
		}
	}

	&__picture {
		@include aPx('width', 260, 180, 1);
		@include aPx('height', 140, 100, 1);
		margin: 0 0 30px;
	}

	&__img {
		object-fit: contain !important;
	}

	&__card-title {
		font-size: 18px;
		line-height: 23px;
		font-weight: 500;
		text-align: center;
		margin: 0 0 17px;
	}

	&__card-text {
		position: relative;
		font-size: 15px;
		line-height: 140%;
		font-weight: 400;
		text-align: left;
	}

	&__card-text--show {
		@media only screen and (max-width: 767px) {
			overflow: hidden;
			text-overflow: ellipsis;
			transition: all .4s ease 0s;

			&:before {
				position: absolute;
				content: '';
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background: linear-gradient(180deg, rgba($rgbaBgModal, 0) 0%, $rgbaBgModal 63.54%);
			}
		}
	}

	&__card-text--top {
		margin: 0 0 38px;
	}

	.sidebar {
		@media only screen and (max-width: 767px) {
			max-width: 100%;
			.sidebar__nav {
				width: 100%;
				display: none;
			}
		}
	}

	&__show-more-btn {
		position: relative;
		align-self: center;
		width: fit-content;
		font-size: 15px;
		line-height: 19px;
		font-weight: 500;

		&:before {
			position: absolute;
			content: '';
			left: 0;
			bottom: -1px;
			width: 100%;
			height: 1px;
			background: $black;
			z-index: 0;
			transition: background .4s ease 0s;
		}

		@media (any-hover: hover) {
			&:hover {
				color: rgba(0, 0, 0, 0.5);

				&:before {
					background: rgba(0, 0, 0, 0.5);
				}
			}
		}
		@media only screen and (min-width: 767px) {
			display: none;
		}
	}

	&__show-more-btn--more {
		margin: 0 0 70px;
	}

	&__show-more-btn--more,
	&__show-more-btn--less {

	}

	&__show-more-btn--less {
		bottom: 1px;
		display: none;
	}
}
